import { Component, ViewChild } from '@angular/core';
import {
    App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController,
    ActionSheetController, Platform
} from 'ionic-angular';
import { Api, GameProvider, StorageProvider, Users, Rank, Translate, Trends } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage, ProfilePage, FirstStep1Page, FirstStepFbPage } from "../pages";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Slides } from 'ionic-angular';
import { appSettings } from '../../app/app-settings';
import { GoogleAnalytics } from '@ionic-native/google-analytics';

@IonicPage()
@Component({
    selector: 'page-loading',
    templateUrl: 'loading.html',
})
export class LoadingPage {
    @ViewChild('slides') slides: Slides;
    currentSlide: number = 0;
    progressBar: number = 1;

    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public viewCtrl: ViewController,
        public imagePicker: ImagePicker,
        public app: App, 
        public api: Api,
        public storage: Storage,
        public alertCtrl: AlertController,
        public camera: Camera,
        public actionSheetCtrl: ActionSheetController, 
        public loadingCtrl: LoadingController,
        public users: Users,
        public storageProvider: StorageProvider,
        public gameProvider: GameProvider,
        public rankProvider: Rank,
        public platform: Platform,
        public translate: Translate,
        public trendsProvider: Trends) {
            this.translate.initTranslate();
    }

    ionViewDidLoad() {
        this.platform.ready().then(() => {
            this.translate.initTranslate();
            this.storage.get(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED).then((val) => {
                if (!val) {
                    this.navCtrl.setRoot(LoginPage);
                }else{
                    this.slides.lockSwipes(true);
                    this.loadingInfos();
                }
            });
        });
    }

    close() {
        this.viewCtrl.dismiss();
    }

    loadingInfos(){
        this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_LAUNCH_PROFILE, true);
        this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_LAUNCH_LOG, true);
        this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_LAUNCH_TREND, true);
        this.storageProvider.loadConfigurations();

        this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then(
            (user) => {
                let data: any;
                data = {};

                this.api.post('getUser/' + user, data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());

                        if(body.error){
                            this.storageProvider.clearConnexionStorage();
                            this.navCtrl.setRoot(LoginPage);
                            return;
                        }

                        this.users.setUser(body);
                        this.saveTokenInApi(this.users);

                        if(this.users.isDeleted()){
                            this.storageProvider.clearConnexionStorage();   
                            this.navCtrl.setRoot(LoginPage);
                        }else if(this.users.isFirstConnexion()){
                            this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_CONNEXION, true);
                            if(this.users.isFacebook()){
                                this.navCtrl.setRoot(FirstStepFbPage);
                            }else{
                                this.navCtrl.setRoot(FirstStep1Page);
                            }
                            return;
                        }else{

                            // Préchargement images
                            let imagePopyDefault = new Image();
                            imagePopyDefault.src = "https://www.popme.app/assets/images/popy_default.png";

                            this.storage.get(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING).then((rank) => {
                                if(!rank){
                                    this.storage.set(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING, 1);

                                };
                            });

                            this.trendsProvider.loadTrends();
                            this.rankProvider.loadAllRanks();
                            this.storage.set(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING, true);
                            let timer = setTimeout(x => {
                                this.gameProvider.loadPopies(20);
                                this.goToSlide();
                            }, 1000);

                            timer = setTimeout(x => {
                                this.navCtrl.setRoot(ProfilePage);
                            }, 2000);
                        }
                    });

            },
            (err) => {
                alert(this.translate.getTranslate('ERROR.NO_CONNECTION'));
                this.storageProvider.clearConnexionStorage();
                this.navCtrl.setRoot(LoginPage);
            }
            );
    }

    goToSlide() {
        this.currentSlide++;
        this.progressBar++;
        this.slides.lockSwipes(false);
        this.slides.slideTo(this.currentSlide, 400);
        this.slides.lockSwipes(true);
    }

    doAlert(message) {
        let alert = this.alertCtrl.create({
            title: 'Aïe!',
            subTitle: message,
            buttons: ['Ok']
        });
        alert.present();
    }

    // Save the token to firestore
    private saveTokenInApi(user) {
        this.platform.ready().then(() => {
            this.storage.get('uid').then((val) => {
                if (val) {
                    let data: any;
                    data = {};
                    data.user = user.id;
                    data.uid = val;
                    if(this.platform.platforms()){
                        data.platform = this.platform.platforms().toString();
                    }
                    this.api.post('updateUid', data)
                    .subscribe(
                        (data) => {
                            //let body: any;
                            //body = JSON.parse(data.text());
                            return false;
                        },
                        (err) => {
                        },
                        () => {
                            //this.goToHome();
                        }
                        );
                }
            });
            return false;
        });
    }

}



