import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController } from 'ionic-angular';
import { Api, Users, Translate } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import { ProfilePage, PlayPage, TrendPage } from "../pages";

/**
 * Generated class for the LogPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
  selector: 'page-log',
  templateUrl: 'log.html',
})
 export class LogPage {

  searchOpen:boolean;
  pops: any[];
  search: string;
  searchResult: any[] = [];
  popsLength: number;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public modalCtrl: ModalController,
    public api: Api,
    public userProvider: Users,
    public translate: Translate) {
    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });
    this.searchOpen = false;
    this.getLastPops(true);
  }

  ionViewDidLoad() {
  }

  filterUsers(event){
    if(this.search.length >= 3){

      let data: any;
      data = {};
      data.search = this.search;

      this.api.post('searchUsers/' + this.userProvider.getId(), data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());

          this.searchResult = body;
        },
        (err) => {
          this.storage.remove('user');
          this.storage.remove('isConnected');
          this.navCtrl.setRoot(LoginPage);
        },
        () => {
          //this.goToHome();
        }
        );
    }else{
      this.searchResult = [];
    }
  }

  goToHisProfile(userId){

    console.log(userId);

    let data: any;
    data = {};

    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
      dismissOnPageChange: true
    });

    loading.present();

    this.api.post('getUserWithPop/' + userId + '/' + this.userProvider.getId(), data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        this.prepareModal(body);

        loading.dismiss();

      },
      (err) => {
        this.storage.remove('user');
        this.storage.remove('isConnected');
        this.navCtrl.setRoot(LoginPage);
        loading.dismiss();
      },
      () => {
        //this.goToHome();
      }
      );
    
  }

  prepareModal(user){
    let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
    profileModal.present();
  }

  pushToProfile() {
    this.navCtrl.setRoot(ProfilePage, {}, {animate: true, direction: 'back'}
      );
  }
  pushToPlay() {
    this.navCtrl.setRoot(PlayPage, {}, {animate: true, direction: 'back'}
      );
  }
  pushToTrend() {
    this.navCtrl.push(TrendPage, {}, {animate: true, direction: 'forward'}
      );
  }
  
  getLastPops(force = false){
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: this.translate.getTranslate('BUTTON.LOADING_DATA')
    });
    loading.present();
    let data: any;
    data = {};
    this.storage.get('configurations').then((configurations) => {
      data.range = +configurations.ACTIVITY_RANGE_DAY.value;
    });
    this.api.post('getLastPops/' + this.userProvider.getId(), data)
    .subscribe(
      (data) => {
        this.pops= [];

        let body: any;
        body = JSON.parse(data.text());
        this.pops = body;
        this.popsLength=0;
        for(let pops in this.pops){
          this.popsLength=this.popsLength+this.pops[pops].pops.length;
        };
        this.storage.set('pops', [this.pops, this.popsLength]);
        loading.dismiss();

      },
      (err) => {
        loading.dismiss();
        this.storage.remove('user');
        this.storage.remove('isConnected');
        this.navCtrl.setRoot(LoginPage);
      },
      () => {
        //this.goToHome();
      }
      );


  }

  doRefresh(event){
    this.getLastPops(true);
    event.complete();
  }
}



