import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";
import { StorageProvider } from "../storage/storage";

@Injectable()
export class Clan {

	private name:string;

	private id:number;

	private description: string;

	private userClans: any[];

	private image: any

	private imageUrl: any;

	private createdAt: string;

	private passCode: string;

	private chat: any[];

	private date: Date;

	private nbChat: number;

	constructor(public api: Api,
		public storage: Storage,
		public storageProvider: StorageProvider) {
	}


	setName(name){
		this.name = name;
	}

	getId(){
		return this.id;
	}

	setId(id){
		this.id = id;
	}

    setChat(chat){
        this.chat = chat;
    }

	setImage(imageBase64){
		this.image = imageBase64;
		this.imageUrl = 'url('+imageBase64+')';
	}

	getImage(){
		return this.image;
	}

	getChat(){
		return this.chat;
	}

	getNbChat(){
		return this.nbChat;
	}

	setNbChat(nb){
		this.nbChat = nb;
	}

	getImageUrl(){
		return this.imageUrl;
	}

	setDescription(description){
		this.description = description;
	}

	getCurrentClan(){
		let result:any = {};
		result.name = this.name;
		result.userClans = this.userClans;
		result.id =this.id;
		result.description = this.description;
		result.image = this.image;
		result.createdAt = this.createdAt;
		result.passCode = this.passCode;
		result.chat = this.chat;
		result.date = this.date;
		result.nbChat = this.nbChat;
		return result;
	}

	setCurrentClan(clan: Clan){
		this.name = clan.name;
		this.userClans = clan.userClans;
		this.id = clan.id;
		this.description = clan.description;
		this.image = clan.image;
		this.createdAt = clan.createdAt;
		this.passCode = clan.passCode;
		this.chat = clan.chat;
		this.date = new Date();
		this.nbChat = clan.nbChat;
	}

	resetClan(){
		this.name =null;
		this.userClans = null;
		this.id = null;
		this.description = null;
		this.image = null;
		this.createdAt = null;
		this.passCode = null;
		this.chat = null;
	}
}



