import { Component } from '@angular/core';
import {
	App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
	LoadingController
} from 'ionic-angular';
import {Api, Translate, Tutoriel, Users } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import { Camera } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';
import {PhotoViewer} from "@ionic-native/photo-viewer";

@IonicPage()
@Component({
	selector: 'page-poperprofile',
	templateUrl: 'poperprofile.html',
})
export class PoperProfilePage {

	userProfile: any = {};
	userRank: any = {};
    user: any = {};
	popies: number[] = [];
    isBlockedByMe: boolean = false;
    isBlocked: boolean = false;

	constructor(public navCtrl: NavController,
		public navParams: NavParams,
		public viewCtrl: ViewController,
		public imagePicker: ImagePicker,
		public app: App, 
		public api: Api,
		public storage: Storage,
		public alertCtrl: AlertController,
		public camera: Camera,
		public actionSheetCtrl: ActionSheetController,
		public loadingCtrl: LoadingController,
		public photoViewer: PhotoViewer,
		public tutoriel: Tutoriel,
        public translate: Translate,
        public userProvider: Users) {
			this.userProfile = this.navParams.get("userProfile");
			this.userRank = this.userProfile.rank;
        	this.user = this.userProvider.getUser();
            this.isBlocked = this.checkIsUserBlocked(this.user.usersBlocked,this.userProfile.id);
            this.isBlockedByMe = this.checkIsUserBlockedByMe(this.user.usersBlockedByMe,this.userProfile.id);
	}

    checkIsUserBlockedByMe(usersBlockedByMe, userProfile) {
        for(let i=0; i<usersBlockedByMe.length;i++){
            if(usersBlockedByMe[i] == userProfile){
                return true;
            }
        }
        return false;
    }

    checkIsUserBlocked(usersBlocked, userProfile) {
        for(let i=0; i<usersBlocked.length;i++){
            if(usersBlocked[i] == userProfile){
                return true;
            }
        }
        return false;
    }

	back() {
		this.viewCtrl.dismiss(this.popies);
		if(this.tutoriel.isTutoriel()){
			this.tutoriel.nextStep({step: 7});
		}
	}

	closeModal(){
	}

	like(index) {
		if(this.userProfile.popies[index].popped !='1' && this.userProfile.popies[index].popped !='0'){
			this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((userId) => {
				let pop: any;
				pop = {};

				this.api.post('pop/' + userId + '/' + this.userProfile.popies[index].id, pop)
				.subscribe(
					(pop) => {
						this.userProfile.popies[index].popped = '1';
						this.popies.push(this.userProfile.popies[index].id);
					},
					(err) => {
                    //this.navCtrl.setRoot(this.navCtrl.getActive().component);
                },
                () => {
                  //this.goToHome();
              }
              );
			});
		}
	}

	viewImg(index){
		if(this.userProfile.popies[index].image){
			this.photoViewer.show(this.userProfile.popies[index].image);
		}
	}

	dislike(index) {
		if(this.userProfile.popies[index].popped !='1' && this.userProfile.popies[index].popped !='0'){
			this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((userId) => {
				let pop: any;
				pop = {};

				this.api.post('unpop/' + userId + '/' + this.userProfile.popies[index].id, pop)
				.subscribe(
					(pop) => {
						console.log("unpop réalisé")
						this.userProfile.popies[index].popped = '0';
						this.popies.push(this.userProfile.popies[index].id);
					},
					(err) => {
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );
			});
		}
	}

    openBlockAccount() {
        let motif = '';
        let actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT'),
            buttons: [
                {
                    text: this.translate.getTranslate('BUTTON.YES'),
                    handler: () => {
                        // Oui
                        this.addBlock();
                        this.isBlockedByMe = true;
                        this.doAlert(this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT_INFORMATIONS'));
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.NO'),
                    handler: () => {
                        // Non
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.CANCEL'),
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

    openUnBlockAccount() {
        let motif = '';
        let actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('PAGE.PLAY.UNBLOCK_ACCOUNT'),
            buttons: [
                {
                    text: this.translate.getTranslate('BUTTON.YES'),
                    handler: () => {
                        // Oui
                        this.deleteBlock();
                        this.isBlockedByMe = false;
                        this.doAlert(this.translate.getTranslate('PAGE.PLAY.UNBLOCK_ACCOUNT_INFORMATIONS'));
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.NO'),
                    handler: () => {
                        // Non
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.CANCEL'),
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

    openReport() {
        let motif = '';
        let actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('PAGE.PROFILE.REPORT_ACCOUNT'),
            buttons: [
                {
                    text: this.translate.getTranslate('PAGE.PROFILE.REPORT_PICTURES'),
                    handler: () => {
                        // contenu choquant
                        motif = '2';
                        this.updateReport(motif);
                    }
                },
                {
                    text: this.translate.getTranslate('PAGE.PROFILE.REPORT_PHOTOS_NO_REASON'),
                    handler: () => {
                        // contenu autre
                        motif = '1';
                        this.updateReport(motif);
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.CANCEL'),
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

    addBlock(){
        let data: any;
        data = {};
        data.userCreated = this.userProvider.getId();
        data.userBlocked = this.userProfile.id;

        this.api.post('block', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                },
                (err) => {
                },
                () => {
                    //this.goToHome();
                }
            );
    }

    deleteBlock(){
        let data: any;
        data = {};
        data.userCreated = this.userProvider.getId();
        data.userBlocked = this.userProfile.id;

        this.api.post('unBlock', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                },
                (err) => {
                },
                () => {
                    //this.goToHome();
                }
            );
    }

    updateReport(motif){
        let data: any;
        data = {};
        data.userCreated = this.userProvider.getId();
        data.userReported = this.userProfile.id;
        data.popyReported = null;
        data.motif = motif;

        this.api.post('report', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                },
                (err) => {
                },
                () => {
                    //this.goToHome();
                    this.doAlert(this.translate.getTranslate('PAGE.PROFILE.REPORT_ALERT_MESSAGE'));
                }
            );
    }

    doAlert(message) {
        let alert = this.alertCtrl.create({
            title: this.translate.getTranslate('BUTTON.INFORMATIONS'),
            subTitle: message,
            buttons: ['Ok']
        });

        alert.present();
    }
}



