import { Component } from '@angular/core';
import { App, IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController, ActionSheetController } from 'ionic-angular';
import { Api, StorageProvider, Users, Translate } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import { LoginPage } from "../login/login";
import { FirstStep2Page } from "../firstStep/firstStep2";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';

@IonicPage()
@Component({
  selector: 'page-first-step',
  templateUrl: 'firstStep1.html',
})
export class FirstStep1Page {

  user: any;
  accountImage: string;
  accountImageUrl: string;
  usualName: string;
  description: string;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController, 
    public loadingCtrl: LoadingController,
    public storageProvider: StorageProvider,
    public userProvider: Users,
    public translate: Translate) {

   this.storage.get(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED).then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   this.storage.get(appSettings.STORAGE_ATTRIBUTES.FIRST_CONNEXION).then((val) => {
    if (!val) {
      this.navCtrl.setRoot(LoginPage);
    }
  });

   let data: any;
   data = {};

   this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((val) => {
    if (val) {
      this.api.post('getUser/'+val, data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());
          
          this.userProvider.setUser(body);

          this.user =this.userProvider.getUser();

          this.accountImageUrl = 'url(' + this.userProvider.getImageAccount() + ')';
          this.description =this.userProvider.getDescription();
        },
        (err) => {
          console.log(err);
          //this.navCtrl.setRoot(this.navCtrl.getActive().component);
        },
        () => {
          //this.goToHome();
        }
        );
    }
  });   
 }

 ionViewDidLoad() {
 }

 close() {
  this.viewCtrl.dismiss();
}

validProfileStep1(){
  this.storage.get('user').then((val) => {
    if (val) {
      let data: any;
      data = {};
      data.description = this.description;
      data.accountImage = this.accountImage;

      let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Etape 1 ...',
        dismissOnPageChange: true
      });

      loading.present();

      this.api.post('updateStep1/' + val, data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());
          if(body.error){
            loading.dismiss();
            this.doAlert(body.message.text);
          }else{
            this.storage.set(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED,true);

            this.userProvider.setUser(body);

            // On redirige l'utilisateur vers sa page si il est authentifié
            this.navCtrl.setRoot(FirstStep2Page);
          }
        },
        (err) => {
          loading.dismiss();
          this.doAlert(err.message);
          //this.navCtrl.setRoot(this.navCtrl.getActive().component);
        },
        () => {
          //this.goToHome();
        }
        );
    }
  });
}

openPhotoPicker() {
  let actionSheet = this.actionSheetCtrl.create({
    title: this.translate.getTranslate('PLUGIN.CAMERA.PICTURE_PROFILE'),
    buttons: [
    {
      text: this.translate.getTranslate('PLUGIN.CAMERA.CHOOSE_PICTURE'),
      role: 'destructive',
      handler: () => {
        const options: CameraOptions = {
          sourceType: 0,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true,
          targetWidth: 500,
          targetHeight: 500,
          allowEdit: true
        }
        this.camera.getPicture(options).then((imageData) => {
          let base64Image = 'data:image/jpeg;base64,' + imageData;
          //this.profileImages[index].image = base64Image;
          this.accountImage = base64Image;
          this.accountImageUrl = 'url('+base64Image+')';
        }, (err) => {
          // Handle error
        });
      }
    },
    {
      text: this.translate.getTranslate('PLUGIN.CAMERA.TAKE_PICTURE'),
      handler: () => {
        const options: CameraOptions = {
          sourceType: 1,
          quality: 50,
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          correctOrientation: true,
          targetWidth: 500,
          targetHeight: 500,
          allowEdit: true
        }
        this.camera.getPicture(options).then((imageData) => {
          // imageData is either a base64 encoded string or a file URI
          // If it's base64 (DATA_URL):
          let base64Image = 'data:image/jpeg;base64,' + imageData;
          //this.profileImages[index].image = base64Image;
          this.accountImage = base64Image;
          this.accountImageUrl = 'url('+base64Image+')';

          // Test insert BACK

        }, (err) => {
          // Handle error
        });
      }
    },
    {
      text: this.translate.getTranslate('BUTTON.CANCEL'),
      role: 'cancel',
      handler: () => {
      }
    }
    ]
  });

  actionSheet.present();
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'Aïe!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}

backToLogin(){
  this.storageProvider.clearConnexionStorage();
  this.navCtrl.setRoot(LoginPage);
}
}



