import { Component } from '@angular/core';
import {
    IonicPage, NavController, NavParams, ModalController, LoadingController, ViewController,
    AlertController, ActionSheetController, Platform
} from 'ionic-angular';
import {Api, Clan, Users, Rank, Translate } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import {ClanEditPage} from "../clan/clanEdit";
import {ClanPage} from "../clan/clan";
import {ProfilePage} from "../profile/profile";
import {LoginPage} from "../login/login";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import {ClanChatPage} from "../clan/clanChat";
import {SocialSharing} from "@ionic-native/social-sharing";
import { Camera } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';


@IonicPage()
@Component({
    selector: 'page-clan',
    templateUrl: 'clanprofile.html',
})
export class ClanProfilePage {

    clan: any = {};
    isVisibleRankingClan: any;
    userClanRole: any;

    constructor(public navCtrl: NavController, 
        public navParams: NavParams,
        public storage: Storage,
        public api: Api,
        public modalCtrl: ModalController,
        public loadingCtrl: LoadingController,
        public viewCtrl: ViewController,
        public alertCtrl: AlertController,
        public actionSheetCtrl: ActionSheetController,
        public socialSharing: SocialSharing,
        public platform: Platform, 
        public camera: Camera,
        public clanProvider: Clan,
        public rankProvider: Rank,
        public userProvider: Users,
        public translate: Translate) {


        let clanInput: any;
        clanInput = {};
        clanInput.clanId = this.navParams.get("clan");
        clanInput.userId = this.userProvider.getId();

        this.api.post('clan/get', clanInput)
        .subscribe(
            (data) => {
                this.clanProvider.setCurrentClan(JSON.parse(data.text()));
                this.clan = this.clanProvider.getCurrentClan();
                this.userClanRole = this.isUserAdmin();
            },
            (err) => {
            },
            () => {
                //this.goToHome();
            }
            );
    }

    ionViewWillEnter(){
        this.clan = this.clanProvider.getCurrentClan();
    }

    showActionsClan(clanId){
    }

    showActions(userId, clanId, role){
        let data: any;
        data = {};
        data.userId = userId;
        data.clanId = clanId;
        const actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('BUTTON.ACTIONS'),
            buttons: [{
                text: this.translate.getTranslate('BUTTON.CANCEL'),
                role: 'cancel',
                handler: () => {
                }
            }]
        });
        if(role == 'ROLE_USER'){
            actionSheet.addButton(
            {
                text: this.translate.getTranslate('BUTTON.UPGRADE'),
                handler: () => {
                    data.role = 'ROLE_ADMIN';
                    this.api.post('clan/grant', data)
                    .subscribe(
                        (data) => {
                            let body: any;
                            body = JSON.parse(data.text());
                            this.clan = body;
                            this.isVisibleRankingClan = true;
                        },
                        (err) => {
                        },
                        () => {
                        }
                        );
                }
            }
            );    
        }   
        if(role == 'ROLE_ADMIN' || role == 'ROLE_CREATEUR'){
            actionSheet.addButton(
            {
                text: this.translate.getTranslate('BUTTON.DOWNGRADE'),
                handler: () => {
                    data.role = 'ROLE_USER';
                    this.api.post('clan/grant', data)
                    .subscribe(
                        (data) => {
                            let body: any;
                            body = JSON.parse(data.text());
                            this.clan = body;
                            this.isVisibleRankingClan = true;
                        },
                        (err) => {
                        },
                        () => {
                        }
                        );
                }
            });    
        }   

        actionSheet.addButton(
        {
            text: this.translate.getTranslate('BUTTON.KICK'),
            handler: () => {
                this.api.post('clan/exclude', data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());
                        this.clan = body;
                        this.isVisibleRankingClan = true;
                    },
                    (err) => {
                    },
                    () => {
                    }
                    );
            }
        });

        actionSheet.addButton({
            text: this.translate.getTranslate('BUTTON.BAN'),
            handler: () => {
                this.api.post('clan/quit', data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());
                        this.clan = body;
                        this.isVisibleRankingClan = true;
                    },
                    (err) => {
                    },
                    () => {
                    }
                    );
            }
        });

        actionSheet.present();
    }

    goToHisProfile(userId){

        let data: any;
        data = {};

        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
          dismissOnPageChange: true
      });

        loading.present();

        this.api.post('getUserWithPop/' + userId + '/' + this.userProvider.getId(), data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());

            this.prepareModal(body);

            loading.dismiss();

        },
        (err) => {
            this.storage.remove('user');
            this.storage.remove('isConnected');
            this.navCtrl.setRoot(LoginPage);
            loading.dismiss();
        },
        () => {
            //this.goToHome();
        }
        );

    }

    prepareModal(user){
        let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
        profileModal.present();
    }

    isUserAdmin(){
        for(let userClan of this.clan.userClans){
            if(userClan.id == this.userProvider.getId()){
                return userClan.role == 'ROLE_ADMIN' || userClan.role == 'ROLE_CREATEUR';
            }
        }
    }

    isNotMe(userId){
        return userId != this.userProvider.getId();
    }

    hasImage(){
        return this.userProvider.getImageAccount() != null;
    }

    back(){
        this.viewCtrl.dismiss();
    }

    goTo(page, direction) {
        this.navCtrl.push(page, {}, {
            direction: direction
        });
    }

    goToEditClan(clanId){
        this.navCtrl.push(ClanEditPage, {clan: clanId}, {direction: 'forward'});
    }

    goToChat(clanId){
        this.navCtrl.push(ClanChatPage, {clan: clanId}, {direction: 'forward'});
    }

    close() {
        this.viewCtrl.dismiss();
    }

    makeDefaultRank() {
      this.storage.set(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING, this.clan.id);
      
      // Chargement de la place de l'utilisateur dans son profil
      this.rankProvider.loadRank(this.userProvider.getUser(), this.clan.id);
  }

  isDefaultRanking() {
    return this.rankProvider.getId() == this.clan.id;
}

showActionsClanQuit(){
    let data: any;
    data = {};
    data.userId = this.userProvider.getId();
    data.clanId = this.clan.id;
    let alert = this.alertCtrl.create({
        title: this.translate.getTranslate('PAGE.CLAN.LEAVE_CLAN'),
        buttons: [
        {
            text: this.translate.getTranslate('BUTTON.CANCEL'),
            role: 'cancel',
            handler: data => {
            }
        },
        {
            text: this.translate.getTranslate('BUTTON.QUIT'),
            handler: () => {
                this.api.post('clan/quit', data)
                .subscribe(
                    (data) => {
                        let body: any;
                        body = JSON.parse(data.text());
                        if(!body.error){
                            if(this.clanProvider.getId() == this.clan.id){
                              this.storage.set(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING, 1);
                              this.rankProvider.loadRank(this.userProvider.getUser(), 1);

                          }
                          this.navCtrl.setRoot(ProfilePage);
                      }
                  },
                  (err) => {
                  },
                  () => {
                  }
                  );
            }
        }
        ]
    });
    alert.present();
}

sharePassword(clan){
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Préparation ...'
  });

    loading.present();
    let data: any;
    data = {};
    data.clan = this.clan;
    this.api.post('shareClan', data)
    .subscribe(
      (data) => {
        loading.dismiss();
        let body: any;
        body = JSON.parse(data.text());
        this.platform.ready().then(() => {

            var options = {
                message: this.translate.getTranslate('PLUGIN.SHARE.CLAN_MESSAGE_1') + clan.name + this.translate.getTranslate('PLUGIN.SHARE.CLAN_MESSAGE_2') + clan.passCode + this.translate.getTranslate('PLUGIN.SHARE.CLAN_MESSAGE_3'),
                subject: '', // fi. for email
                files: [body], // an array of filenames either locally or remotely
                url: '',
                chooserTitle: this.translate.getTranslate('PAGE.LOGIN.PASSWORD') // Android only
            }

            this.socialSharing.shareWithOptions(options)
            .then(() => {
                //this.showSuccessShareMsg();
            })
            .catch((err) => {
                //this.showErrorShareMsg(err);
            });


        });
    },
    (err) => {
        this.doAlert(err.message);
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
    },
    () => {
        //this.goToHome();
    }
    );
}

doAlert(message) {
    let alert = this.alertCtrl.create({
      title: 'Aïe!',
      subTitle: message,
      buttons: ['Ok']
  });

    alert.present();
}


}


