import {Component, NgZone} from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';
import {Storage} from "@ionic/storage";


declare var google;

@IonicPage()
@Component({
    selector: 'page-auto-complete',
    templateUrl: 'autoComplete.html',
})

export class AutoCompletePage {
    autocompleteItems;
    autocomplete;

    user: any = {};
    infos: any = {};
    latitude: number = 0;
    longitude: number = 0;

    service = new google.maps.places.AutocompleteService();

    constructor (public viewCtrl: ViewController, private zone: NgZone, public storage: Storage) {
        this.autocompleteItems = [];
        this.autocomplete = {
            query: ''
        };
    }

    dismiss() {
        this.viewCtrl.dismiss(this.infos);
    }

    chooseItem(item: any) {
        this.getInfos(item);
    }

    updateSearch() {

        if (this.autocomplete.query == '') {
            this.autocompleteItems = [];
            return;
        }

        let me = this;
        this.service.getPlacePredictions({
            input: this.autocomplete.query,
            componentRestrictions: {
                country: 'fr'
            },
            types:['(cities)']
        }, (predictions, status) => {
            me.autocompleteItems = [];

            me.zone.run(() => {
                if (predictions != null) {
                    predictions.forEach((prediction) => {
                        me.autocompleteItems.push(prediction.description);
                    });
                }
            });
        });
    }

    getInfos(address:any) {

        let city;
        let region;
        let country;
        let latitude;
        let longitude;

        let geocoder = new google.maps.Geocoder();
        geocoder.geocode({ 'address': address }, (results, status) => {
                if (results[0]) {
                    console.log(results[0]);
                    latitude = results[0].geometry.location.lat();
                    longitude = results[0].geometry.location.lng();
                    for (var j=0; j<results.length; j++)
                    {
                        if (results[j].types[0]=='locality')
                        {
                            break;
                        }
                    }
                    for (var i=0; i<results[j].address_components.length; i++)
                    {
                        if (results[j].address_components[i].types[0] == "locality") {
                            //this is the object you are looking for City
                            city = results[j].address_components[i];
                        }
                        if (results[j].address_components[i].types[0] == "administrative_area_level_1") {
                            //this is the object you are looking for State
                            region = results[j].address_components[i];
                        }
                        if (results[j].address_components[i].types[0] == "country") {
                            //this is the object you are looking for
                            country = results[j].address_components[i];
                        }
                    }
                    //city data
                    //alert(city.long_name + " || " + region.long_name + " || " + country.long_name);
                    this.infos.city = city.long_name;
                    this.infos.region = region.long_name;
                    this.infos.country = country.long_name;
                    this.infos.latitude = latitude;
                    this.infos.longitude = longitude;
                    this.infos.placeId = results[0].place_id;
                    //alert("lat: " + this.infos.latitude + ", long: " + this.infos.longitude);
                    this.storage.set('location', this.infos);
                    this.viewCtrl.dismiss(this.infos);
                } else {
                    alert("No results found");
                }
                //}
        });
    }

}


