import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Users } from '../users/users';
import { Storage } from "@ionic/storage";
import { StorageProvider } from "../storage/storage";
import { appSettings } from '../../app/app-settings';

@Injectable()
export class Trends {
	private popers:any[];

	private popeds:any[];

	private firstPopy:any;

	constructor(public api: Api,
		public storage: Storage,
		public storageProvider: StorageProvider,
		public userProvider: Users) {
	}

	getPopers(){
		return this.popers;
	}

	getPopeds(){
		return this.popeds;
	}

	getFirstPopy(){
		return this.firstPopy;
	}

	loadTrends(){
		this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((user) => {
			let poped: any;
			poped = {};
			this.api.post('trend/poped', poped)
			.subscribe(
				(data) => {
					this.popeds= [];
					let body: any;
					body = JSON.parse(data.text());
					for(let i=0; i<body.length;i++){
						this.popeds[i]=body[i];
					}
					let poper: any;
					poper = {};
					this.api.post('trend/poper', poper)
					.subscribe(
						(data) => {
							this.popers= [];
							let body: any;
							body = JSON.parse(data.text());
							for(let i=0; i<body.length;i++){
								this.popers[i]=body[i];
							}
							let popy: any;
							popy = {};
							popy.userId = this.userProvider.getId();
							this.api.post('trend/first-popy', popy)
							.subscribe(
								(data) => {
									this.firstPopy = JSON.parse(data.text());
								},
								(err) => {
								},
								() => {
									//this.goToHome();
								}
								);
						},
						(err) => {
						},
						() => {
							//this.goToHome();
						}
						);
				},
				(err) => {
				},
				() => {
					//this.goToHome();
				}
				);
		});
	}
}



