import { Component, NgZone } from '@angular/core';
import {
  IonicPage, NavController, NavParams, AlertController, Platform, ViewController, ModalController,
  LoadingController, ActionSheetController
} from 'ionic-angular';
import {Storage} from "@ionic/storage";
import { Facebook } from '@ionic-native/facebook';
import { StorageProvider, Users, Api, Translate } from "../../providers/providers";
import { Geolocation } from '@ionic-native/geolocation';
import { LoginPage, AutoCompletePage } from '../pages';
import { Camera, CameraOptions } from '@ionic-native/camera';

/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
 export class SettingsPage {

  user: any = {};
  location: any = {};
  rank: any = {};
  maxTime: number;
  remainingTime: string;
  lang: string;
  hidevalue: boolean=true;
  distance: number = 80;
  ageRange: object = {lower: 18, upper: 30};
  dateB: any;
  accountImage: string;
  accountImageUrl: string;
  isNotificationPictureUpdate: boolean;
  isNotificationResume: boolean;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
    public storage: Storage, 
    public fb: Facebook,
    public storageProvider: StorageProvider,
    public alertCtrl: AlertController,
    public geolocation: Geolocation,
    public platform: Platform,
    public viewCtrl: ViewController,
    public zone: NgZone,
    public modalCtrl: ModalController,
    public loadingCtrl: LoadingController,
    public api: Api,
    public actionSheetCtrl: ActionSheetController, 
    public camera: Camera,
    public userProvider: Users,
    public translate : Translate) {

    this.lang = this.translate.getDefaultLang();
    this.loadUserInfo(this.userProvider.getId());
    this.storage.get('location').then((locationStorage) => {
      if (locationStorage) {
        this.location = locationStorage;
      }
    });
    this.geolocation.getCurrentPosition().then((resp) => {
      // resp.coords.latitude
      // resp.coords.longitude
    }).catch((error) => {
    });
    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
      // data can be a set of coordinates, or an error (if an error occurred).
      // data.coords.latitude
      // data.coords.longitude
    });
  }

  setNotificationResume(){
    if(this.isNotificationResume){
      this.user.notificationResume = '1';
    }else{
      this.user.notificationResume = '0';
    }
  }

  setNotificationPictureUpdate(){
    if(this.isNotificationPictureUpdate){
      this.user.notificationPictureUpdate = '1';
    }else{
      this.user.notificationPictureUpdate = '0';
    }
  }

  ionViewWillLeave() {
    let data: any;
    data = {};
    data = this.user;
    data.birthday = new Date(this.dateB);
    data.place = this.location;
    data.notificationPictureUpdate = this.user.notificationPictureUpdate;
    data.notificationResume = this.user.notificationResume;

    if(this.accountImage){
      data.accountImage = this.accountImage;
    }

    this.api.post('updateUser/' + this.userProvider.getId(), data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        if(!body.error){
          this.userProvider.setUser(body);
        }
      },
      (err) => {
      },
      () => {
        //this.goToHome();
      }
      );
    this.translate.setDefaultLang(this.lang);
  }

  showAddressModal () {
    let modal = this.modalCtrl.create(AutoCompletePage);

    modal.onDidDismiss(data => {
      this.storage.get('location').then((infos) => {
        if (infos) {
          this.location = infos;

          let data: any;
          data = {};
          data = this.user;
          data.birthday = new Date(this.dateB);
          data.place = this.location;
          this.api.post('updateUser/' + this.user.id, data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              if(!body.error){
                this.userProvider.setUser(body);
              }
            },
            (err) => {
            },
            () => {
              //this.goToHome();
            }
            );
        }
      });

    });
    modal.present();
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
  }

  logout() {
    const confirm = this.alertCtrl.create({
      title: this.translate.getTranslate('PAGE.SETTINGS.ACCOUNT.SIGN_OUT'),
      message: this.translate.getTranslate('PAGE.SETTINGS.ACCOUNT.SIGN_OUT_CONFIRM'),
      buttons: [
      {
        text: this.translate.getTranslate('BUTTON.NO'),
        handler: () => {
        }
      },
      {
        text: this.translate.getTranslate('BUTTON.YES'),
        handler: () => {
          this.fb.logout()
          .then(res => false)
          .catch(e => console.log('Error logout from Facebook', e));
          this.storageProvider.clearConnexionStorage();
          this.navCtrl.setRoot(LoginPage);
        }
      }
      ]
    });
    confirm.present();
  }

  isSocialUser(){
    return this.userProvider.isFacebook();
  }

  deleteAccount() {
    const confirm = this.alertCtrl.create({
      title: this.translate.getTranslate('PAGE.SETTINGS.ACCOUNT.DELETE_TITLE'),
      message: this.translate.getTranslate('PAGE.SETTINGS.ACCOUNT.DELETE_CONFIRM'),
      buttons: [
      {
        text: this.translate.getTranslate('BUTTON.NO'),
        handler: () => {
        }
      },
      {
        text: this.translate.getTranslate('BUTTON.YES'),
        handler: () => {
          let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: this.translate.getTranslate('BUTTON.LOADING_DELETE_ACCOUNT'),
            dismissOnPageChange: true
          });

          loading.present();

          let data: any;
          data = {};

          this.api.post('deleteAccount/' + this.userProvider.getId(), data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());

              this.storage.remove('user');
              this.storage.remove('isConnected');
              this.navCtrl.setRoot(LoginPage);
            },
            (err) => {
            },
            () => {
              //this.goToHome();
            }
            );
          loading.dismiss();
        }
      }
      
      ]
    });
    confirm.present();
  }

  loadUserInfo(idUser){
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
      dismissOnPageChange: true
    });

    loading.present();

    let data: any;
    data = {};

    this.api.post('getUser/' + idUser, data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        this.rank = this.user.rank;

        this.userProvider.setUser(body);
        this.user =this.userProvider.getUser();

        console.log(this.userProvider.getBirthday());
        console.log(this.userProvider.getBirthday().date);

        this.dateB = new Date(this.userProvider.getBirthday().date);
        var curr_date =  this.dateB.getDate();
        var curr_month =  this.dateB.getMonth() < 9 ? '0'+(this.dateB.getMonth()+1) : this.dateB.getMonth() + 1; //Months are zero based
        var curr_year =  this.dateB.getFullYear();
        this.dateB = curr_year + "-" + curr_month + "-" + curr_date;console.log(this.user);

        // Notifications
        if(this.user.notificationResume == '1'){
          this.isNotificationResume = true;
        }else{
          this.isNotificationResume = false;
        }console.log(this.isNotificationResume);console.log(this.user.notificationResume);
        if(this.user.notificationPictureUpdate == '1'){
          this.isNotificationPictureUpdate = true;
        }else{
          this.isNotificationPictureUpdate = false;
        }console.log(this.isNotificationPictureUpdate);console.log(this.user.notificationPictureUpdate);

      },
      (err) => {
        this.storage.remove('user');
        this.storage.remove('isConnected');
        this.navCtrl.setRoot(LoginPage);
      },
      () => {
        //this.goToHome();
      }
      );
    loading.dismiss();
  }

  openPhotoPicker() {
    let actionSheet = this.actionSheetCtrl.create({
      title: this.translate.getTranslate('PLUGIN.CAMERA.PICTURE_PROFILE'),
      buttons: [
      {
        text: this.translate.getTranslate('PLUGIN.CAMERA.CHOOSE_PICTURE'),
        handler: () => {
          const options: CameraOptions = {
            sourceType: 0,
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            correctOrientation: true,
            targetWidth: 500,
            targetHeight: 500
          }
          this.camera.getPicture(options).then((imageData) => {
            let base64Image = 'data:image/jpeg;base64,' + imageData;
            //this.profileImages[index].image = base64Image;
            this.accountImage = base64Image;
            this.accountImageUrl = 'url('+base64Image+')';
          }, (err) => {
            // Handle error
          });
        }
      },
      {
        text: this.translate.getTranslate('PLUGIN.CAMERA.TAKE_PICTURE'),
        handler: () => {
          const options: CameraOptions = {
            sourceType: 1,
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            correctOrientation: true,
            targetWidth: 500,
            targetHeight: 500
          }
          this.camera.getPicture(options).then((imageData) => {
            // imageData is either a base64 encoded string or a file URI
            // If it's base64 (DATA_URL):
            let base64Image = 'data:image/jpeg;base64,' + imageData;
            //this.profileImages[index].image = base64Image;
            this.accountImage = base64Image;
            this.accountImageUrl = 'url('+base64Image+')';

            // Test insert BACK

          }, (err) => {
            // Handle error
          });
        }
      },
      {
        text: this.translate.getTranslate('BUTTON.CANCEL'),
        role: 'cancel',
        handler: () => {
        }
      }
      ]
    });

    actionSheet.present();
  }

}






