import { Component, ViewChild, ViewChildren, QueryList } from '@angular/core';
import {
    IonicPage, NavController, ModalController, LoadingController, ActionSheetController,
    AlertController
} from 'ionic-angular';
import { Api, GameProvider, Users, Tutoriel, Translate } from '../../providers/providers';
import { Storage } from "@ionic/storage";
import { PoperProfilePage } from "../poperprofile/poperprofile";
import { LoginPage } from "../login/login";
import { ProfilePage, LogPage, TrendPage } from "../pages";
import 'rxjs/Rx';

import {
	Direction,
	StackConfig,
	SwingStackComponent,
	SwingCardComponent
} from 'angular2-swing';
import {CameraOptions} from "@ionic-native/camera";

@IonicPage()
@Component({
	selector: 'page-play',
	templateUrl: 'play.html',
})
export class PlayPage {
	@ViewChild('cardStack') swingStack: SwingStackComponent;
	@ViewChildren('card') swingCards: QueryList<SwingCardComponent>;

	stackConfig: StackConfig;
	popies: any[];
	isLoading: boolean = true;
	user : any;
	userProfiled: any = {};
	cards: any = this.gameProvider.getCards();

	constructor(public navCtrl: NavController, 
		public storage: Storage,
		public api: Api,
		public modalCtrl: ModalController,
		public loadingCtrl: LoadingController,
		public gameProvider: GameProvider,
		public userProvider: Users,
		public tutoriel: Tutoriel,
		public translate: Translate,
		public actionSheetCtrl: ActionSheetController,
        public alertCtrl: AlertController) {
		this.stackConfig = {
			// Default setting only allows UP, LEFT and RIGHT so you can override this as below
			allowedDirections: [
			Direction.LEFT,
			Direction.RIGHT
			],
			throwOutConfidence: (offsetX, offsetY, element) => {
				return Math.min(Math.abs(offsetX) / (element.offsetWidth / 2), 1);
			},
			transform: (element, x, y, r) => {
				this.onItemMove(element, x, y, r);
			},
			throwOutDistance: (d) => {
				return 800;
			}
		};
	}

	ngAfterViewInit() {
		this.gameProvider.addNewCards(this.popies);
	}

	// Called whenever we drag an element
	onItemMove(element, x, y, r) {
		let nope = element.querySelector('.stamp-nope');
		let like = element.querySelector('.stamp-like');
		let caculatedValue = Math.min(100, Math.abs(x) - 20) / 100;// 0 - 1

		if (x < 0 && Math.abs(x) > 20) {
			nope.style.opacity = caculatedValue;
		} else {
			like.style.opacity = caculatedValue;
		}

		element.style['transform'] = `translate3d(0, 0, 0) translate(${x}px, ${y}px) rotate(${r}deg)`;

		// Zoom effect for the cards underneath
		if (this.swingCards.toArray()[1] != undefined){
			let cardBehind = this.swingCards.toArray()[1].getNativeElement();
			cardBehind.style['transform'] = `scale(${0.94 + 0.06 * caculatedValue}, ${0.94 + 0.06 * caculatedValue})`;
		}
	}


	disliked() {
		let removedCard = this.gameProvider.getCards().shift();

		let pop: any;
		pop = {};

		this.api.post('unpop/' + this.userProvider.getId() + '/' + removedCard.id, pop)
		.subscribe(
			(pop) => {
				console.log("unpop réalisé");
				if(this.tutoriel.isTutoriel()){
					this.tutoriel.nextStep({popped: false});
				}
			},
			(err) => {
				console.log(err);
				//this.navCtrl.setRoot(this.navCtrl.getActive().component);
			},
			() => {
				//this.goToHome();
			}
			);

		this.gameProvider.getCardsPlayed().push(removedCard.id);
		this.gameProvider.getMoreCardsIfNecessary();

	}

	liked() {
		let removedCard = this.gameProvider.getCards().shift();
		//this.checkMatching(removedCard);

		let pop: any;
		pop = {};

		this.api.post('pop/' + this.userProvider.getId() + '/' + removedCard.id, pop)
		.subscribe(
			(pop) => {
				console.log("pop réalisé");
				if(this.tutoriel.isTutoriel()){
					this.tutoriel.nextStep({popped: true});
				}
			},
			(err) => {
				console.log(err);
				//this.navCtrl.setRoot(this.navCtrl.getActive().component);
			},
			() => {
				//this.goToHome();
			}
			);

		this.gameProvider.getCardsPlayed().push(removedCard.id);
		this.gameProvider.getMoreCardsIfNecessary();

	}

	goToHisProfile(){
		let card = this.cards[0];

		if(card.user.id == this.userProfiled.id){
			// Pas besoin de chargement
			this.prepareModal(this.userProfiled);
		}else{

			let data: any;
			data = {};

			let loading = this.loadingCtrl.create({
				spinner: 'crescent',
				content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
				dismissOnPageChange: true
			});

			loading.present();

			this.api.post('getUserWithPop/' + card.user.id + '/' + this.userProvider.getId(), data)
			.subscribe(
				(data) => {
					let body: any;
					body = JSON.parse(data.text());

					this.userProfiled = body;
					this.prepareModal(body);

					loading.dismiss();

				},
				(err) => {
					this.storage.remove('user');
					this.storage.remove('isConnected');
					this.navCtrl.setRoot(LoginPage);
					loading.dismiss();
				},
				() => {
					//this.goToHome();
				}
				);
		}
	}

	prepareModal(user){
		let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
		profileModal.onDidDismiss(popiesPlayed => {
			for(let popy in popiesPlayed){

				let found = this.gameProvider.getCards().findIndex(function(element) {
					return element.id == popiesPlayed[popy];
				});

				if(found>-1){
					this.gameProvider.getCardsPlayed().push(this.cards[found].id);
					this.gameProvider.getCards().splice(found, 1);
				}

			}
		});
		profileModal.present();
	}


	trackByFn(index, item) {
		return item.id;
	}

	pushToProfile() {
		this.navCtrl.setRoot(ProfilePage, {}, {animate: true, direction: 'back'}
			);
	}
	pushToLog() {
		this.navCtrl.setRoot(LogPage, {}, {animate: true, direction: 'forward'}
			);
	}
	pushToTrend() {
		this.navCtrl.push(TrendPage, {}, {animate: true, direction: 'forward'}
			);
	}

	refresh(){
		let loading = this.loadingCtrl.create({
			spinner: 'crescent',
			content: this.translate.getTranslate('BUTTON.LOADING_CARDS')
		});

		loading.present();
		this.gameProvider.clearCards();
		this.gameProvider.addPopies(20, this.userProvider.getId());
		this.cards =this.gameProvider.getCards();
		loading.dismiss();
	}

	openReport() {
		let motif = '';
		let actionSheet = this.actionSheetCtrl.create({
			title: this.translate.getTranslate('PAGE.PLAY.REPORT_PHOTOS'),
			buttons: [
			{
				text: this.translate.getTranslate('PAGE.PLAY.REPORT_PHOTOS_BAD_CONTENT'),
				handler: () => {
					// contenu choquant
					motif = '2';
					this.updateReport(motif);
                    this.doAlert(this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT_INFORMATIONS'));
					this.disliked();
				}
			},
			{
				text: this.translate.getTranslate('PAGE.PLAY.REPORT_PHOTOS_NO_REASON'),
				handler: () => {
					// contenu autre
					motif = '1';
					this.updateReport(motif);
                    this.doAlert(this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT_INFORMATIONS'));
					this.disliked();
				}
			},
			{
				text: this.translate.getTranslate('BUTTON.CANCEL'),
				role: 'cancel',
				handler: () => {
				}
			}
			]
		});
		actionSheet.present();
	}

    openReportAccount() {
        let motif = '';
        let actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT'),
            buttons: [
                {
                    text: this.translate.getTranslate('BUTTON.YES'),
                    handler: () => {
                        // Oui
                        this.addBlock();
                        this.doAlert(this.translate.getTranslate('PAGE.PLAY.BLOCK_ACCOUNT_INFORMATIONS'));
                        this.disliked();
                        this.refresh();
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.NO'),
                    handler: () => {
                        // Non
                    }
                },
                {
                    text: this.translate.getTranslate('BUTTON.CANCEL'),
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

    doAlert(message) {
        let alert = this.alertCtrl.create({
            title: this.translate.getTranslate('BUTTON.INFORMATIONS'),
            subTitle: message,
            buttons: ['Ok']
        });

        alert.present();
    }

    addBlock(){
        let card = this.cards[0];
        let data: any;
        data = {};
        data.userCreated = this.userProvider.getId();
        data.userBlocked = card.user.id;

        this.api.post('block', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                },
                (err) => {
                },
                () => {
                    //this.goToHome();
                }
            );
    }

	updateReport(motif){
		let card = this.cards[0];
		let data: any;
		data = {};
		data.userCreated = this.userProvider.getId();
		data.userReported = card.user.id;
		data.popyReported = card.id;
		data.motif = motif;

		this.api.post('report', data)
		.subscribe(
			(data) => {
				let body: any;
				body = JSON.parse(data.text());
			},
			(err) => {
			},
			() => {
				//this.goToHome();
			}
			);
	}

}



