import { Component } from '@angular/core';
import {IonicPage, LoadingController, ModalController, NavController, NavParams, Platform} from 'ionic-angular';
import {LoginPage} from "../login/login";
import {Api, Users, Trends, Translate } from "../../providers/providers";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import {Storage} from "@ionic/storage";
import {RelativeRankPage} from "../rank/relativeRank";
import {PhotoViewer} from "@ionic-native/photo-viewer";
import { ProfilePage, PlayPage, LogPage } from "../pages";

/**
 * Generated class for the TrendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
   selector: 'page-trend',
   templateUrl: 'trend.html',
 })
 export class TrendPage {

   user: any;
   popeds: any[];
   popers: any[];
   firstPopy: any;
   display: number = 1;

   constructor(public navCtrl: NavController,
     public navParams: NavParams,
     public loadingCtrl: LoadingController,
     public api: Api,
     public modalCtrl: ModalController,
     public storage: Storage,
     public platform: Platform,
     public photoViewer: PhotoViewer,
     public translate: Translate,
     public userProvider: Users,
     public trendsProvider: Trends) {

    this.platform.ready().then(() => {

      this.storage.get('isConnected').then((val) => {
       if (!val) {
         this.navCtrl.setRoot(LoginPage);
       }
     });

      this.firstPopy = this.trendsProvider.getFirstPopy();
      this.popeds = this.trendsProvider.getPopeds();
      this.popers = this.trendsProvider.getPopers();

    });

    this.display = 1;
  }

  isReadyFirstPopy(){
   return this.firstPopy !== undefined;
 }

 show(index){
  this.display =index;
}

dislike(idPopy) {

  let pop: any;
  pop = {};

  this.api.post('unpop/' + this.userProvider.getId() + '/' + idPopy, pop)
  .subscribe(
    (pop) => {
      console.log("unpop réalisé");
      this.firstPopy.popped = true;
    },
    (err) => {
      console.log(err);
      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
    },
    () => {
      //this.goToHome();
    }
    );
}

like(idPopy) {

  let pop: any;
  pop = {};

  this.api.post('pop/' + this.userProvider.getId() + '/' + idPopy, pop)
  .subscribe(
    (pop) => {
      console.log("pop réalisé");
      this.firstPopy.popped = true;
    },
    (err) => {
      console.log(err);
      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
    },
    () => {
      //this.goToHome();
    }
    );
}

goToHisProfile(userId){

  // Interdiction d'accéder à son profil vue VISITEUR
  if(userId == this.userProvider.getId()){
   return;
 }

 let data: any;
 data = {};

 let loading = this.loadingCtrl.create({
   spinner: 'crescent',
   content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
   dismissOnPageChange: true
 });

 loading.present();

 this.api.post('getUserWithPop/' + userId + '/' + this.userProvider.getId(), data)
 .subscribe(
   (data) => {
     let body: any;
     body = JSON.parse(data.text());

     this.prepareModal(body);

     loading.dismiss();

   },
   (err) => {
     loading.dismiss();
   },
   () => {
     //this.goToHome();
   }
   );

}

prepareModal(user){
 let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
 profileModal.present();
}

prepareModalRanking(type){
 let profileModal = this.modalCtrl.create(RelativeRankPage, { userProfile: type });
 profileModal.present();
}

ionViewDidLoad() {
}

pushToProfile() {
  this.navCtrl.setRoot(ProfilePage, {}, {animate: true, direction: 'back'}
    );
}
pushToPlay() {
  this.navCtrl.setRoot(PlayPage, {}, {animate: true, direction: 'back'}
    );
}
pushToLog() {
  this.navCtrl.setRoot(LogPage, {}, {animate: true, direction: 'back'}
    );
}

viewImg(){
  this.photoViewer.show(this.firstPopy.image, 'Popy', {share: true});
}

isUserConnected(){
  return this.firstPopy.user.id == this.userProvider.getId();
}
}



