import { Component } from '@angular/core';
import { IonicPage, ViewController, NavController, NavParams } from 'ionic-angular';
import {LogPage, PlayPage, RelativeRankPage} from "../pages";

@IonicPage()
@Component({
	selector: 'popover',
	templateUrl: 'popover-rank.html',
})
export class PopoverRankPage {

	rank: any;
	constructor(public viewCtrl: ViewController, public navCtrl:NavController, public navParams:NavParams) {
		this.rank = this.navParams.get('rank');
	}

	close() {
		this.viewCtrl.dismiss();
	}

	pushToRelativeRank() {
		this.navCtrl.push(RelativeRankPage, {}, {animate: true, direction: 'forward'});
		this.viewCtrl.dismiss();
	}

}


