import { Component, ViewChild } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController, Content
} from 'ionic-angular';
import {Api, Clan, Users, Rank, Translate } from "../../providers/providers";
import {Storage} from "@ionic/storage";
import { Camera, CameraOptions } from '@ionic-native/camera';
import {ProfilePage} from "../profile/profile";
import {ClanProfilePage} from "../clan/clanprofile";
import {ClanPage} from "../clan/clan";
import { appSettings } from '../../app/app-settings';

@IonicPage()
@Component({
  selector: 'page-clanChat',
  templateUrl: 'clanChat.html',
})
export class ClanChatPage {
  users: any;
  clanId: any;
  clan: any = {};
  isVisibleRankingClan: any;
  hasChangePicture: boolean = false;

  message:string;
  indicate:boolean = false;

  encours:boolean = false;
  timer:any;

  messages: any[] = [];
  typingMessage: string = '';
  showGiphy: boolean = false;
  @ViewChild(Content) content: Content;

  constructor(public navCtrl: NavController, 
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public api: Api,
    public params: NavParams,
    public storage: Storage,
    public actionSheetCtrl: ActionSheetController, 
    public camera: Camera,
    public clanProvider: Clan,
    public userProvider: Users,
    public rankProvider: Rank,
    public translate: Translate) {
    if(params.get('clan')){
      this.clan = clanProvider.getCurrentClan();
      this.message = "";
    }else{
      this.clan = {};
      clanProvider.resetClan();
    }
  }

  ionViewWillLeave() {
    clearInterval(this.timer);
  }

  //scrolls to bottom whenever the page has loaded
  ionViewDidEnter(){
    this.scrollBottom();
  }

  ionViewDidLoad() {
    let clanInput: any;
    clanInput = {};
    clanInput.clanId = this.clanProvider.getId();
    clanInput.userId = this.userProvider.getId();
    this.api.post('clan/access', clanInput).subscribe(
      (data) => {
        this.clanProvider.setNbChat(0);
      },
      (err) => {
      },
      () => {
        //this.goToHome();
      }
      );
    this.storage.get('configurations').then((configurations) =>{
      /*if(configurations['LIVE_CONTROL'].value == 1){
          console.log('test3');
        this.timer = setInterval(x => {
            console.log('test4');
          let live: any;
          live = {};
          live.clanId = this.clanProvider.getId();
          live.userId = this.userProvider.getId();

          this.api.post('chat/live', live)
          .subscribe(
            (data) => {
              this.encours = (data.text() == "true");
            },
            (err) => {
              this.encours = false;
            },
            () => {
              //this.goToHome();
            }
            );
        }, 2000);
      }*/
    });
  }

  setDefaultClan(flag){
  }

  close() {
  }

  indicateInput(){
    if(!this.indicate && this.message.length > 0){
      this.indicate = true;
      let indicate: any;
      indicate = {};
      indicate.clanId = this.clanProvider.getId();
      indicate.userId = this.userProvider.getId();
      indicate.action = true;

      this.api.post('chat/indicate', indicate)
      .subscribe(
        (data) => {
        },
        (err) => {

          this.indicate = false;
        },
        () => {
          //this.goToHome();
        }
        );
    }

    if(this.indicate && this.message.length == 0){
      this.indicate = false;
      let indicate: any;
      indicate = {};
      indicate.clanId = this.clanProvider.getId();
      indicate.userId = this.userProvider.getId();
      indicate.action = false;

      this.api.post('chat/indicate', indicate)
      .subscribe(
        (data) => {
        },
        (err) => {
          this.indicate = true;
        },
        () => {
          //this.goToHome();
        }
        );
    }
  }

  sendMessage(){
    let data: any;
    data = {};
    data.clanId = this.clanProvider.getId();
    data.userId = this.userProvider.getId();
    data.message = this.message;
    data.type = 'text';
    this.api.post('chat/add', data)
    .subscribe(
      (result) => {

        this.api.post('clan/get', data)
        .subscribe(
          (data) => {
            this.clanProvider.setCurrentClan(JSON.parse(data.text()));
            this.clan = this.clanProvider.getCurrentClan();
            this.message="";
            this.scrollBottom();
          },
          (err) => {
          },
          () => {
            //this.goToHome();
          }
          );

      },
      (err) => {
        this.doAlert(err.message);
      },
      () => {
      }
      );
  }

  deleteItem(id, index){
    const confirm = this.alertCtrl.create({
      title: 'Supprimer ce message',
      message: 'Veux-tu vraiment supprimer ce message ?',
      buttons: [
      {
        text: 'Non',
        handler: () => {
        }
      },
      {
        text: 'Oui',
        handler: () => {
          let data: any;
          data = {};
          data.messageId = id;
          this.api.post('chat/delete', data)
          .subscribe(
            (result) => {
              this.clanProvider.getChat().splice(index,1);
            },
            (err) => {
              this.doAlert(err.message);
            },
            () => {
            }
            );
        }
      }
      ]
    });
    confirm.present();
  }

  doAlert(message, title='Aïe!') {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['Ok']
    });

    alert.present();
  }

  sendGif(imageUrl) {

    let data: any;
    data = {};
    data.clanId = this.clanProvider.getId();
    data.userId = this.userProvider.getId();
    data.message = imageUrl;
    data.type = 'image';
    this.api.post('chat/add', data)
    .subscribe(
      (result) => {

        this.api.post('clan/get', data)
        .subscribe(
          (data) => {
            this.clanProvider.setCurrentClan(JSON.parse(data.text()));
            this.clan = this.clanProvider.getCurrentClan();
            this.message="";
            this.scrollBottom();
          },
          (err) => {
          },
          () => {
            //this.goToHome();
          }
          );

      },
      (err) => {
        this.doAlert(err.message);
      },
      () => {
      }
      );
  }


  fakeReply() {
    setTimeout(() => {
      this.messages.push({
        isMe: false,
        avatar: 'assets/img/hieu.png',
        type: 'text',
        body: 'Nice. Keep typing dude',
        timestamp: 'Oct 10, 2017 9:55am'
      });

      this.scrollBottom();
    }, 500);
  }

  scrollBottom() {
    this.content.resize();
    this.content.scrollTo(0, this.content.scrollHeight, 350);
  }

  toggleGiphy() {
    this.showGiphy = !this.showGiphy;
    this.content.resize();
  }


}


