import { Component } from '@angular/core';
import {
  App, IonicPage, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController
} from 'ionic-angular';
import {Api, Translate } from "../../providers/providers";
import {LoginPage} from "../login/login";
import {Storage} from "@ionic/storage";

@IonicPage()
@Component({
  selector: 'page-rankpopover',
  templateUrl: 'rankpopover.html',
})
export class RankpopoverPage {

  user: any;
  users: any;
  clanId: any;
  clan: any;
  isVisibleRankingClan: any;

  constructor(public viewCtrl: ViewController,
              public loadingCtrl: LoadingController,
              public alertCtrl: AlertController,
              public api: Api,
              public params: NavParams,
              public storage: Storage,
              public actionSheetCtrl: ActionSheetController,
              public translate: Translate) {

      this.isVisibleRankingClan = false;
      this.clanId = params.get('clan');
      this.storage.get('user').then((user) => {
          this.user = user;
          this.getInfosClan(this.clanId);
          this.getListUsersClan(this.clanId);
      });

  }

    setDefaultClan(flag){

    }

    refresh(){
      this.isVisibleRankingClan = true;
        this.getListUsersClan(this.clanId);
    }

    getInfosClan(clanId){
        let data: any;
        data = {};
        data.clanId = clanId;
        this.api.post('clan/get', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                    this.clan = body;
                },
                (err) => {
                },
                () => {
                }
            );
    }

    getListUsersClan(clanId){
        let data: any;
        data = {};
        data.clanId = clanId;

        let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: this.translate.getTranslate('BUTTON.REGISTER'),
            dismissOnPageChange: true
        });

        loading.present();

        this.api.post('getUsersByClan', data)
            .subscribe(
                (data) => {
                    let body: any;
                    body = JSON.parse(data.text());
                    this.users = body;
                    this.isVisibleRankingClan = true;
                    loading.dismiss();
                },
                (err) => {
                    loading.dismiss();
                },
                () => {
                }
            );
    }

    showActions(userId, clanId){
        let data: any;
        data = {};
        data.userId = userId;
        data.clanId = clanId;
        const actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('BUTTON.ACTIONS'),
            buttons: [
                {
                    text: this.translate.getTranslate('BUTTON.UPGRADE'),
                    handler: () => {
                        data.role = 'ROLE_ADMIN';
                        this.api.post('clan/grant', data)
                            .subscribe(
                                (data) => {
                                    let body: any;
                                    body = JSON.parse(data.text());
                                    this.users = body;
                                    this.isVisibleRankingClan = true;
                                },
                                (err) => {
                                },
                                () => {
                                    this.getListUsersClan(clanId);
                                }
                            );
                    }
                },{
                    text: this.translate.getTranslate('BUTTON.DOWNGRADE'),
                    handler: () => {
                        data.role = 'ROLE_USER';
                        this.api.post('clan/grant', data)
                            .subscribe(
                                (data) => {
                                    let body: any;
                                    body = JSON.parse(data.text());
                                    this.users = body;
                                    this.isVisibleRankingClan = true;
                                },
                                (err) => {
                                },
                                () => {
                                    this.getListUsersClan(clanId);
                                }
                            );
                    }
                },{
                    text: this.translate.getTranslate('BUTTON.KICK'),
                    handler: () => {
                        this.api.post('clan/exclude', data)
                            .subscribe(
                                (data) => {
                                    let body: any;
                                    body = JSON.parse(data.text());
                                    this.users = body;
                                    this.isVisibleRankingClan = true;
                                },
                                (err) => {
                                },
                                () => {
                                    this.getListUsersClan(clanId);
                                }
                            );
                    }
                },{
                    text: this.translate.getTranslate('BUTTON.DELETE'),
                    handler: () => {
                        this.api.post('clan/quit', data)
                            .subscribe(
                                (data) => {
                                    let body: any;
                                    body = JSON.parse(data.text());
                                    this.users = body;
                                    this.isVisibleRankingClan = true;
                                },
                                (err) => {
                                },
                                () => {
                                    this.getListUsersClan(clanId);
                                }
                            );
                    }
                },{
                    text: this.translate.getTranslate('BUTTON.CANCEL'),
                    role: 'cancel',
                    handler: () => {
                    }
                }
            ]
        });
        actionSheet.present();
    }

  close() {
    this.viewCtrl.dismiss();
  }

}


