import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Users } from '../users/users';
import { Storage } from "@ionic/storage";

@Injectable()
export class GameProvider {
	user: any;
	cardsPlayed: any[] = [];
	cards: any[] = [];

	constructor(public api: Api,
		public storage: Storage,
		public userProvider: Users) {
	}

	loadPopies(count) {
		this.addPopies(count, this.userProvider.getId());
	}

	addPopies(count, userId){
		let popies: any;
		popies = {};
		popies.reject= [];
		for(let card in this.cardsPlayed){
			popies.reject.push(this.cardsPlayed[card]);
		}
		for(let card in this.cards){
			popies.reject.push(this.cards[card].id);
		}		

		let newPopies: any[];
		newPopies = [];
		this.api.post('getGamePopies/' + userId + '/' + count, popies)
		.subscribe(
			(popies) => {
				let body: any;
				body = JSON.parse(popies.text());
				for(let popy in body){
					let image = new Image();
					image.src = body[popy]['image'];
					newPopies.push({
						id: body[popy]['id'],
						image: body[popy]['image'],
						user: {
							usualName : body[popy]['user']['usualName'],
							accountImage : body[popy]['user']['accountImage'],
							id : body[popy]['user']['id']
						}
					});
				}
				this.addNewCards(newPopies);
			},
			(err) => {
				console.log(err);
				alert("kos");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );
	}

	// Add new cards to our array
	addNewCards(popies) {
		this.cards.push(...popies);
	}


	getMoreCardsIfNecessary() {
		if (this.cards.length < 10) {
			this.addPopies(10, this.userProvider.getId());
		}
	}

	clearCards(){
		this.cards = [];
	}

	getCards(){
		return this.cards;
	}

	getCardsPlayed(){
		return this.cardsPlayed;	
	}

}



