import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, ModalController, LoadingController, App, AlertController, Slides, ViewController } from 'ionic-angular';
import {Api, StorageProvider, Translate, Users} from '../../providers/providers';
import { Storage } from "@ionic/storage";
import { ProfilePage } from "../profile/profile";
import { LoadingPage } from "../loading/loading";
import { FirstStep1Page } from "../firstStep/firstStep1";
import { Facebook } from '@ionic-native/facebook';
import { appSettings } from '../../app/app-settings';

//import {page} from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login-classic',
  templateUrl: 'loginClassic.html',
})
export class LoginClassicPage {

  introSlides: any;
  profilePage = ProfilePage;
  user: any;
  password: any;
  loaded: boolean;
  isLoggedIn: boolean = false;
  usersFb: any;
  @ViewChild('slides') slides: Slides;
  @ViewChild('mailInput') mailInput;

  constructor(
    public navCtrl: NavController, 
    public app: App, 
    public api: Api, 
    public storage: Storage, 
    public loadingCtrl: LoadingController, 
    public alertCtrl: AlertController, 
    public fb: Facebook,
    public storageProvider: StorageProvider,
    public modalCtrl: ModalController,
    public viewCtrl: ViewController,
    public userProvider: Users,
    public translate: Translate) {


    this.loaded = false;
    this.storage.get('isConnected').then((val) => {
      if (val) {
        this.navCtrl.setRoot(ProfilePage);
      }
    });
    this.loaded = true;

    this.storage.get(appSettings.STORAGE_ATTRIBUTES.USER).then((val) => {
      if (val) {
        this.user = val;
      }
    });
  }

  ionViewDidLoad() {
    console.log(this.mailInput);
  }

  back() {
    this.viewCtrl.dismiss();
  }

  goTo(page) {
    this.app.getRootNav().setRoot(page)
    .then(() => {

    })
  }

  checkLogin(){
    let data: any;
    data = {};
    data.user = this.user;
    data.password = this.password;

    this.api.post('connectUser', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        if(body.error){
          this.showAlertBadAccount(body.message.text);
        }else{
          this.userProvider.setUser(body);
          this.storage.set(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED,true);
          this.storage.set(appSettings.STORAGE_ATTRIBUTES.USER, this.userProvider.getId());
          if(!body.enabled){
            if(!this.userProvider.isDeleted()){
              this.storage.set(appSettings.STORAGE_ATTRIBUTES.FIRST_CONNEXION,true);
              this.navCtrl.setRoot(FirstStep1Page);
            }else{
              // User qui a supprimer son compte
              this.showAlertBadAccount(this.translate.getTranslate('PAGE.LOGIN.DELETED_ACCOUNT'));
            }
          }else{
            this.navCtrl.setRoot(LoadingPage);
          }
        }
      },
      (err) => {
        console.log(err);
        this.showAlertBadAccount(this.translate.getTranslate('PAGE.LOGIN.BAD_ACCOUNT'));
        //alert("ko");
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
      },
      () => {
        //this.goToHome();
      }
      );
  }

  reinitPassword(){
    let data: any;
    data = {};
    data.email = this.user;

    if(!this.user){
     const alert = this.alertCtrl.create({
      title: this.translate.getTranslate('PAGE.LOGIN.PASSWORD_REINIT_TITLE'),
      subTitle: this.translate.getTranslate('PAGE.LOGIN.PASSWORD_REINIT_SUBTITLE'),
      buttons: ['OK']
    });
     alert.present();
   } else {
    this.api.post('reinitPassword', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        if(body){
          const alert = this.alertCtrl.create({
            title: this.translate.getTranslate('PAGE.LOGIN.PASSWORD_REINIT_PROGRESS_TITLE'),
            subTitle: this.translate.getTranslate('PAGE.LOGIN.PASSWORD_REINIT_PROGRESS_SUBTITLE'),
            buttons: ['OK']
          });
          alert.present();
        }

      },
      (err) => {
        console.log(err);
        this.showAlertBadAccount(this.translate.getTranslate('PAGE.LOGIN.BAD_ACCOUNT'));
        //alert("ko");
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
      },
      () => {
        //this.goToHome();
      }
      );
  }

}

showAlertBadAccount(message) {
  const alert = this.alertCtrl.create({
    title: this.translate.getTranslate('ERROR.NO_CONNECTION'),
    subTitle: message,
    buttons: ['OK']
  });
  alert.present();
}
}


