import { Component } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController, PopoverController, Platform, ModalController
} from 'ionic-angular';
import { Api, Users, Rank, Tutoriel, Translate, PopyTimer } from '../../providers/providers';
import { ImagePicker } from '@ionic-native/image-picker';
import { Storage } from "@ionic/storage";
import {LoginPage, PopoverScorePage, PopoverRankPage, PopoverTimePage, PlayPage, LogPage, TrendPage, SettingsPage, RelativeRankPage, ClanPage, PreviewPage } from "../pages";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Globalization } from '@ionic-native/globalization';
import { appSettings } from '../../app/app-settings';
import { SocialSharing } from '@ionic-native/social-sharing';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Screenshot } from '@ionic-native/screenshot';
import {PoperProfilePage} from "../poperprofile/poperprofile";
import { Crop } from '@ionic-native/crop';
import { Base64 } from '@ionic-native/base64';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  user: any = {};
  rank: any = {};

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public imagePicker: ImagePicker,
    public app: App, 
    public api: Api,
    public storage: Storage,
    public alertCtrl: AlertController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController,
    public globalization: Globalization,
    public popoverCtrl: PopoverController,
    public socialSharing: SocialSharing,
    public photoViewer: PhotoViewer,
    public platform: Platform,
    public screenshot: Screenshot,
    public userProvider: Users,
    public rankProvider: Rank,
    public modalCtrl: ModalController,
    public tutoriel: Tutoriel,
    public translate: Translate,
    public crop: Crop,
    public base64: Base64,
    public popyTimer: PopyTimer) {

    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });

    this.user = this.userProvider.getUser();
    this.storage.get(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING).then((fromLoading) => {
      if(fromLoading){
        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: this.translate.getTranslate('BUTTON.LOADING'),
          dismissOnPageChange: true
        });

        loading.present();
        this.storage.get(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING).then((rank) => {
          if(rank){
            this.rank = this.rankProvider.loadRank(this.user, rank);  
          }else{
            this.rank = this.rankProvider.loadRank(this.user, 1);
          }
        });


        loading.dismiss();

        this.storage.remove(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING);
        
        this.popyTimer.initTimer();
        this.popyTimer.startTimer();

        //this.storage.set('launchTutoriel', true);
        this.storage.get('launchTutoriel').then((val) => {
          if(val){
            this.tutoriel.setTutoriel();
            if(tutoriel.getCurrentStep() == 0){
              tutoriel.nextStep({});
            }
            this.storage.set('launchTutoriel', false);
          }
        });
      }else{
        this.storage.get(appSettings.STORAGE_ATTRIBUTES.FIRST_LAUNCH_PROFILE).then((val) => {
          this.storage.remove(appSettings.STORAGE_ATTRIBUTES.FIRST_LAUNCH_PROFILE);
          this.loadUserInfo(this.userProvider.getId(), val);
        });
      }
    });
  }

  getPreferredLanguage(){
    this.globalization.getPreferredLanguage()
    .then(res => alert(res.value))
    .catch(e => alert(e));
  }

  getLocaleName(){
    this.globalization.getLocaleName()
    .then(res => alert(res.value))
    .catch(e => alert(e));
  }

  loadUserInfo(idUser, force = false){
    this.storage.get('user').then((userStorage) => {
      if (userStorage && !force) {
        this.user = this.userProvider.getUser();

        this.storage.get(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING).then((rank) => {
          if(rank){
            this.rank = this.rankProvider.loadRank(this.user, rank);

          }else{
            this.rank = this.rankProvider.loadRank(this.user, 1);   
          }
        });

      }else{
        let loading = this.loadingCtrl.create({
          spinner: 'crescent',
          content: this.translate.getTranslate('BUTTON.LOADING_DATA'),
          dismissOnPageChange: true
        });

        loading.present();

        let data: any;
        data = {};

        this.api.post('getUser/' + idUser, data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());

            this.userProvider.setUser(body);

            this.user = this.userProvider.getUser();

            this.storage.get(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING).then((rank) => {
              if(rank){
                this.rank = this.rankProvider.loadRank(this.user, rank);  
              }else{
                this.rank = this.rankProvider.loadRank(this.user, 1); 
              }
            });

            this.popyTimer.initTimer();
            this.popyTimer.startTimer();
          },
          (err) => {
            this.storage.remove('user');
            this.storage.remove('isConnected');
            this.navCtrl.setRoot(LoginPage);
          },
          () => {
            //this.goToHome();
          }
          );
        loading.dismiss();
      }
    });
  }

  isClasse(){
    if(this.rank !== undefined && this.rank.id != null){
      return this.rankProvider.getNumber() !== undefined;
    }
    return false;
  }

  ionViewDidLoad() {
  }

  close() {
    this.viewCtrl.dismiss();
  }

  doRefresh(event){
    this.loadUserInfo(this.userProvider.getId(), true);

    if(this.tutoriel.isTutoriel() && this.tutoriel.getCurrentStep() == 8){
      this.tutoriel.nextStep({});
    }

    event.complete();
  }

  openPhotoPicker(index) {
    if(this.user.popies[index].image){
      this.doAlert(this.translate.getTranslate('PLUGIN.CAMERA.FULL_STOCK'));
    }else{
      let actionSheet = this.actionSheetCtrl.create({
        title: this.translate.getTranslate('PLUGIN.CAMERA.MODIFY_ALBUM'),
        buttons: [
        {
          text: this.translate.getTranslate('PLUGIN.CAMERA.CHOOSE_PICTURE'),
          handler: () => {
            const options: CameraOptions = {
              sourceType: 0,
              quality: 50,
              destinationType: this.camera.DestinationType.DATA_URL,
              encodingType: this.camera.EncodingType.JPEG,
              mediaType: this.camera.MediaType.PICTURE,
              correctOrientation: true,
              allowEdit: true
            }

            this.camera.getPicture(options).then((imageData) => {
              let base64Image = 'data:image/jpeg;base64,' + imageData;

              this.user.popies[index].image = base64Image;
              this.user.popies[index].encours=true;
              let data: any;
              data = {};
              data.popy = base64Image;

              this.api.post('addPopy/' + this.user.id, data)
              .subscribe(
                (data) => {
                  let body: any;
                  body = JSON.parse(data.text());
                  if(body.error){
                    this.user.popies[index].encours=false;
                    this.doAlert(body.message.text);
                  }else{
                    let imagePopy = new Image();
                    imagePopy.src = body.image;
                    this.user.popies[index].image = body.image;
                    this.user.popies[index].state = body.state;
                    this.user.popies[index].encours=false;
                    this.loadUserInfo(this.userProvider.getId(), true);
                  }
                },
                (err) => {
                  this.user.popies[index].encours=false;
                  this.doAlert(err.message);
                },
                () => {
                }
                );
            }, (err) => {

            });

          }
        },
        {
          text: this.translate.getTranslate('PLUGIN.CAMERA.TAKE_PICTURE'),
          handler: () => {
            const options: CameraOptions = {
              sourceType: 1,
              quality: 50,
              destinationType: this.camera.DestinationType.DATA_URL,
              encodingType: this.camera.EncodingType.JPEG,
              mediaType: this.camera.MediaType.PICTURE,
              correctOrientation: true
            }
            this.camera.getPicture(options).then((imageData) => {
              // imageData is either a base64 encoded string or a file URI
              // If it's base64 (DATA_URL):
              let base64Image = 'data:image/jpeg;base64,' + imageData;
              this.user.popies[index].image = base64Image;
              this.user.popies[index].encours=true;

              let data: any;
              data = {};
              data.popy = base64Image;

              this.api.post('addPopy/' + this.user.id, data)
              .subscribe(
                (data) => {
                  let body: any;
                  body = JSON.parse(data.text());
                  if(body.error){
                    this.user.popies[index].encours=false;
                    this.doAlert(body.message.text);
                  }else{
                    let imagePopy = new Image();
                    imagePopy.src = body.image;
                    this.user.popies[index].image = body.image;
                    this.user.popies[index].state = body.state;
                    this.user.popies[index].encours=false;
                    this.loadUserInfo(this.userProvider.getId(), true);
                  }
                },
                (err) => {
                  this.user.popies[index].encours=false;
                  this.doAlert(err.message);
                },
                () => {
                }
                );

            }, (err) => {
            });
          }
        },
        {
          text: this.translate.getTranslate('BUTTON.CANCEL'),
          role: 'cancel',
          handler: () => {
          }
        }
        ]
      });
actionSheet.present();
}
}

isLoading(popy){
  return popy.encours;
}

removeImage(index) {
  let alert = this.alertCtrl.create({
    title: this.translate.getTranslate('PLUGIN.CAMERA.DELETE_PICTURE'),
    message: this.translate.getTranslate('PLUGIN.CAMERA.DELETE_PICTURE_ALERT'),
    buttons: [
    {
      text: this.translate.getTranslate('BUTTON.CANCEL'),
      role: 'cancel',
      handler: () => {
      }
    },
    {
      text: this.translate.getTranslate('BUTTON.DELETE'),
      handler: () => {

        this.user.popies[index].image = "";

        let data: any;
        data = {};

        this.api.post('deletePopy/' + this.user.id 
          + '/' +this.user.popies[index].id, data)
        .subscribe(
          (data) => {
            let body: any;
            body = JSON.parse(data.text());
            if(body.error){
              this.doAlert(body.message.text);
            }
          },
          (err) => {
            this.doAlert(err.message);
            //this.navCtrl.setRoot(this.navCtrl.getActive().component);
          },
          () => {
            //this.goToHome();
          }
          );

        this.user.popies[index].image = "";
      }
    }
    ]
  });
  alert.present();
}

presentPopoverScore(event){
  console.log(event);
  let popover = this.popoverCtrl.create(PopoverScorePage);
  popover.present({
    ev: event
  });
}

presentPopoverRank(event){
  console.log(event);
  let popover = this.popoverCtrl.create(PopoverRankPage, {rank:this.rankProvider.getModel()});
  popover.present({
    ev: event
  });
}

presentPopoverTime(event){
  console.log(event);
  let popover = this.popoverCtrl.create(PopoverTimePage);
  popover.present({
    ev: event
  });
}

goTo(page, direction) {
  this.navCtrl.setRoot(page, {},
    {animate: true, direction: direction}
    );
}

pushToPlay() {
  this.navCtrl.setRoot(PlayPage, {}, {animate: true, direction: 'forward'}
    );
}
pushToLog() {
  this.navCtrl.setRoot(LogPage, {}, {animate: true, direction: 'forward'}
    );
}
pushToTrend() {
  this.navCtrl.setRoot(TrendPage, {}, {animate: true, direction: 'forward'}
    );
}
pushToSettings() {
  this.navCtrl.push(SettingsPage, {}, {animate: true, direction: 'forward'}
    );
}
pushToRelativeRank() {
  this.navCtrl.push(RelativeRankPage, {}, {animate: true, direction: 'forward'}
    );
}
pushToClan() {
  this.navCtrl.push(ClanPage, {}, {animate: true, direction: 'forward'}
    );
}

doAlert(message) {
  let alert = this.alertCtrl.create({
    title: 'Aïe!',
    subTitle: message,
    buttons: ['Ok']
  });

  alert.present();
}

viewImg(index, share){
  if(this.user.popies[index].image){
    if(share){
      if(this.user.popies[index].pop_count == 0){
        this.photoViewer.show(this.user.popies[index].image_real, 'Aucun pop recu', {share: share});
      }
      if(this.user.popies[index].pop_count == 1){
        this.photoViewer.show(this.user.popies[index].image_real, this.user.popies[index].pop_count + ' pop recu', {share: share});
      }
      if(this.user.popies[index].pop_count > 1){
        this.photoViewer.show(this.user.popies[index].image_real, this.user.popies[index].pop_count + ' pops recus', {share: share});
      }
    }else{
      this.photoViewer.show(this.user.popies[index].image_real, null, {share: share});
    }
  }
}

shareViaSMS(){
  let loading = this.loadingCtrl.create({
    spinner: 'crescent',
    content: 'Préparation ...'
  });

  loading.present();
  let data: any;
  data = {};
  data.user = this.userProvider.getForShare();
  data.rank = this.rankProvider.getRankForShare();
  this.api.post('shareProfile/' + this.user.id, data)
  .subscribe(
    (data) => {
      loading.dismiss();
      let body: any;
      body = JSON.parse(data.text());
      this.platform.ready().then(() => {
        // Take a screenshot and get temporary file URI
        this.screenshot.URI(100)
        .then((res) => {

          var options = {
            message: this.translate.getTranslate('PLUGIN.SHARE.MESSAGE'),
            subject: '', // fi. for email
            files: [body], // an array of filenames either locally or remotely
            url: 'https://popme.app/loading',
            chooserTitle: this.translate.getTranslate('PLUGIN.SHARE.TITLE') // Android only
          }

          this.socialSharing.shareWithOptions(options)
          .then(() => {
            //this.showSuccessShareMsg();
          })
          .catch((err) => {
            //this.showErrorShareMsg(err);
          });

        }, (err) => {

        });
      });
    },
    (err) => {
      this.doAlert(err.message);
      //this.navCtrl.setRoot(this.navCtrl.getActive().component);
    },
    () => {
      //this.goToHome();
    }
    );


}

presentPopoverPreview(img){
  let profileModal = this.modalCtrl.create(PreviewPage, { img: img });
  profileModal.onDidDismiss(coucou => {

  });
  profileModal.present();
}

popyIsValid(popy){
  return popy.state >= 100 && popy.state < 200;
}

}


