import { Component } from '@angular/core';
import {
    IonicPage, NavController, ModalController, LoadingController, ViewController,
    AlertController, ActionSheetController
} from 'ionic-angular';
import { Api, Users, Rank, Translate } from '../../providers/providers';
import {Storage} from "@ionic/storage";
import {ClanProfilePage} from "../clan/clanprofile";
import {ClanEditPage} from "../clan/clanEdit";
import {Camera, CameraOptions} from "@ionic-native/camera";
import {LoginPage} from "../login/login";
import {SettingsPage} from "../pages";


@IonicPage()
@Component({
    selector: 'page-clan',
    templateUrl: 'clan.html',
})
export class ClanPage {

    user: any;
    clans: any;
    displayMenu: any;
    password: any;
    clanName: any;
    clanImage: string;
    clanImageUrl: string;

    constructor(public navCtrl: NavController, 
        public storage: Storage,
        public api: Api,
        public modalCtrl: ModalController,
        public loadingCtrl: LoadingController,
        public viewCtrl: ViewController,
        public alertCtrl: AlertController,
        public actionSheetCtrl: ActionSheetController,
        public camera: Camera,
        public userProvider: Users,
        public rankProvider: Rank,
        public translate: Translate) {


    }

    ionViewWillEnter() {   
        this.displayMenu = 1;
        this.listClans();
        
    }

    doRefresh(event){
        this.listClans();
        event.complete();
    }

    listClans(){
        let data: any;
        data = {};
        data.userId = this.userProvider.getId();

        this.api.post('clans/get', data)
        .subscribe(
            (data) => {
                let body: any;
                body = JSON.parse(data.text());
                this.clans = body;
            },
            (err) => {
                //this.navCtrl.setRoot(this.navCtrl.getActive().component);
            },
            () => {
                //this.goToHome();
            }
            );
    }

    showKey(key){
        let alert = this.alertCtrl.create({
            title: this.translate.getTranslate('PAGE.LOGIN.PASSWORD'),
            subTitle: key,
            buttons: ['OK']
        });
        alert.present();
    }

    close() {
        this.viewCtrl.dismiss();
    }

    goToClanProfile(index){
        //let profileModal = this.modalCtrl.create(ClanProfilePage, { clan: index });
        //profileModal.present();
        this.navCtrl.push(ClanProfilePage, {clan: index}, {animate: true, direction: 'forward'});
    }

    setDisplayMenu(menu){
        this.displayMenu = menu;
    }

    showMessage(key, message){
        let alert = this.alertCtrl.create({
            title: key,
            subTitle: message,
            buttons: ['OK']
        });
        alert.present();
    }

    goToCreateClan(){
        //this.navCtrl.push(ClanEditPage, {direction: 'forward'});
        this.navCtrl.push(ClanEditPage, {}, {animate: true, direction: 'forward'});
    }

    joinClan(){
        let data: any;
        data = {};
        data.passCode = this.password;
        data.userId = this.userProvider.getId();

        let loading = this.loadingCtrl.create({
            spinner: 'crescent',
            content: this.translate.getTranslate('BUTTON.LOADING'),
            dismissOnPageChange: true
        });

        loading.present();

        this.api.post('clan/join', data)
        .subscribe(
            (data) => {
                let body: any;
                body = JSON.parse(data.text());
                if(body.error){
                    loading.dismiss();
                    this.showMessage('Erreur', body.message.text);
                }else{
                    loading.dismiss();
                    this.rankProvider.loadAllRanks();
                    this.navCtrl.push(ClanProfilePage, {"clan": body.id}, {animate: true, direction: 'forward'});
                    this.displayMenu = 1;
                }
            },
            (err) => {
                loading.dismiss();
                //this.navCtrl.setRoot(this.navCtrl.getActive().component);
            },
            () => {
                //this.goToHome();
            }
            );
    }

    openPhotoPicker() {
        let actionSheet = this.actionSheetCtrl.create({
            title: this.translate.getTranslate('PLUGIN.CAMERA.PICTURE_PROFILE'),
            buttons: [
            {
                text: this.translate.getTranslate('PLUGIN.CAMERA.CHOOSE_PICTURE'),
                handler: () => {
                    const options: CameraOptions = {
                        sourceType: 0,
                        quality: 100,
                        destinationType: this.camera.DestinationType.DATA_URL,
                        encodingType: this.camera.EncodingType.JPEG,
                        mediaType: this.camera.MediaType.PICTURE,
                        correctOrientation: true,
                        targetWidth: 500,
                        targetHeight: 500
                    }
                    this.camera.getPicture(options).then((imageData) => {
                        let base64Image = 'data:image/jpeg;base64,' + imageData;
                        //this.profileImages[index].image = base64Image;
                        this.clanImage = base64Image;
                        this.clanImageUrl = 'url('+base64Image+')';
                    }, (err) => {
                        // Handle error
                    });
                }
            },
            {
                text: this.translate.getTranslate('PLUGIN.CAMERA.TAKE_PICTURE'),
                handler: () => {
                    const options: CameraOptions = {
                        sourceType: 1,
                        quality: 100,
                        destinationType: this.camera.DestinationType.DATA_URL,
                        encodingType: this.camera.EncodingType.JPEG,
                        mediaType: this.camera.MediaType.PICTURE,
                        correctOrientation: true,
                        targetWidth: 500,
                        targetHeight: 500
                    }
                    this.camera.getPicture(options).then((imageData) => {
                        // imageData is either a base64 encoded string or a file URI
                        // If it's base64 (DATA_URL):
                        let base64Image = 'data:image/jpeg;base64,' + imageData;
                        //this.profileImages[index].image = base64Image;
                        this.clanImage = base64Image;
                        this.clanImageUrl = 'url('+base64Image+')';

                        // Test insert BACK

                    }, (err) => {
                        // Handle error
                    });
                }
            },
            {
                text: this.translate.getTranslate('BUTTON.CANCEL'),
                role: 'cancel',
                handler: () => {
                }
            }
            ]
        });

        actionSheet.present();
    }

}



