import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { ClanProfilePage } from './clanprofile';
import { SwingModule } from 'angular2-swing';
import {TranslateModule} from "@ngx-translate/core";

@NgModule({
  declarations: [
    ClanProfilePage
  ],
  imports: [
    IonicPageModule.forChild(ClanProfilePage),
    SwingModule,
    TranslateModule.forRoot()
  ],
  exports: [
    ClanProfilePage
  ]
})
export class ClanProfilePageModule {}



