import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Users } from '../users/users';
import { Storage } from "@ionic/storage";
import { StorageProvider } from "../storage/storage";
import { Translate } from "../translate/translate";

@Injectable()
export class Rank {
	private id:number;

	private number:number;

	private text:string;

	private rank:any;

	private image:string;

	private name:string;

	private allRanks:any[];

	constructor(public api: Api,
		public storage: Storage,
		public storageProvider: StorageProvider,
		public userProvider: Users,
		public translate: Translate) {
	}

	getId(){
		return this.id;
	}

	setId(id){
		this.id = id;
	}

	getNumber(){
		return this.number;
	}

	setNumber(number){
		this.number = number;
	}

	getText(){
		return this.text;
	}

	setText(text){
		this.text = text;
	}

	setRank(rank){
		this.rank = rank;
	}

	getRank(){
		return this.rank;
	}

	setName(name){
		this.name = name;
	}

	getName(){
		return this.name;
	}

	setImage(image){
		this.image = image;
	}

	getImage(){
		return this.image;
	}

	getModel(){
		return {
			'number':this.number,
			'text': this.text,
			'image': this.image,
			'name': this.name,
			'id': this.id,
			'rank': this.rank
		};;
	}

	setAllRanks(ranks){
		this.allRanks = ranks;
	}

	loadRank(user, rank){
		switch (rank) {
			case 1:{
				if(user.rank.world){
					this.setId(rank);
					this.setRank(user.rank.world);
					this.setNumber(user.rank.world.number);
					this.setText(user.rank.world.text);
					this.setImage("world");
					this.setName(this.translate.getTranslate('PAGE.RANKING.MENU_WORLD'));
				}else{
					this.setId(null);
					this.setRank(null);
					this.setNumber(null);
					this.setText(null);
					this.setImage(null);
					this.setName(null);
				}
				break;
			}
			case 2:{
				this.setId(rank);
				this.setRank(user.rank.country);
				this.setNumber(user.rank.country.number);
				this.setText(user.rank.country.text);
				this.setImage("country");
				this.setName(user.place.country);
				break;
			}
			case 3:{
				this.setId(rank);
				this.setRank(user.rank.city);
				this.setNumber(user.rank.city.number);
				this.setText(user.rank.city.text);
				this.setImage("city");
				this.setName(user.place.city);
				break;
			}
			default:{
				// Recuperation du classement du clan
				let rankChosen = this.allRanks[rank];
				if(!rankChosen){
					this.loadRank(user, 1);
				}else{
					this.setNumber(rankChosen.number);
					this.setText(rankChosen.text);
					this.setImage(rankChosen.image);
					this.setName(rankChosen.name);
					this.setId(rank);
				}
				break;
			}
		}
		return this.getModel();
	}


	loadAllRanks(){
		let data: any;
		data = {};
		data.userId = this.userProvider.getId();

		this.api.post('ranking/rank/clan', data)
		.subscribe(
			(data) => {
				let body: any;
				body = JSON.parse(data.text());
				this.allRanks = body;
				for(let rank in this.allRanks){
					let image = new Image();
					image.src = this.allRanks[rank].image;
				}
			},
			(err) => {
			},
			() => {
			}
			);
	}

	getRankForShare(){
		return {
			'number':this.number,
			'text': this.text,
			'image': this.image,
			'name': this.name,
			'id': this.id
		};
	}
}



