import { Injectable } from '@angular/core';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";
import { StorageProvider } from "../storage/storage";
import { Users } from "../users/users";

@Injectable()
export class PopyTimer {

	private timer:any;

	private maxTime:number;

	private remainingTime:string;

	private hidevalue:boolean;

	constructor(public api: Api,
		public storage: Storage,
		public storageProvider: StorageProvider,
		public userProvider: Users) {
	}

	getMaxTime(){
		return this.maxTime;
	}

	getRemainingTime(){
		return this.remainingTime;
	}

	getHideValue(){
		return this.hidevalue;
	}
	
	initTimer(){
		this.storage.get('configurations').then((configurations) => {
			switch(this.userProvider.getNbTotalPopies()){
				case 1:{
					this.maxTime = +configurations['BLOCK_ADD_SECOND_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy(); 
					break;
				}
				case 2:{
					this.maxTime = +configurations['BLOCK_ADD_THIRD_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy(); 
					break;
				}
				case 3:{
					this.maxTime = +configurations['BLOCK_ADD_FOURTH_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy(); 
					break;
				}
				default:{
					this.maxTime = +configurations['BLOCK_ADD_POPY_DURATION'].value - this.userProvider.getTimeWithoutNewPopy(); 
					break;
				}
			}

			if(this.maxTime <= 0) { 
				this.maxTime -= 1;
			}
			if(this.maxTime>0) {
				this.remainingTime = this.determineTimeText();
				this.hidevalue = false;
			} else {
				this.hidevalue = true;
			}

		});
	}

	startTimer(){
		this.stopTimer();
		this.timer = setTimeout(x => 
		{
			if(this.maxTime >= 0) { 
				this.maxTime -= 1;
			}
			if(this.maxTime>0) {
				this.remainingTime = this.determineTimeText();
				this.hidevalue = false;
				this.startTimer();
			} else {
				this.hidevalue = true;
			}
		}, 1000);
	}

	stopTimer(){
		clearTimeout(this.timer);
	}

	determineTimeText(){
		var days = Math.floor(this.maxTime / 86400);
		var hours = Math.floor((this.maxTime - (days * 86400)) / 3600);
		var minutes = Math.floor((this.maxTime - (hours * 3600)) / 60);
		var seconds = (this.maxTime - (hours * 3600) - (minutes * 60));
		if(days > 0){  
			return days + "j " + hours + "h ";
		}else{
			if(hours > 0){  
				return hours + "h " + minutes + "m ";
			}else{
				if(minutes > 0){
					return minutes + "m " + seconds + "s";
				}else{
					return seconds + "s";
				}
			}
		} 
	}

}



