import { Component } from '@angular/core';
import {
  App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
  LoadingController
} from 'ionic-angular';
import {Api, Clan, Users, Rank, Translate } from "../../providers/providers";
import {Storage} from "@ionic/storage";
import { Camera, CameraOptions } from '@ionic-native/camera';
import {ProfilePage} from "../profile/profile";
import {ClanProfilePage} from "../clan/clanprofile";
import {ClanPage} from "../clan/clan";
import { appSettings } from '../../app/app-settings';

@IonicPage()
@Component({
  selector: 'page-clanEdit',
  templateUrl: 'clanEdit.html',
})
export class ClanEditPage {

  users: any;
  clanId: any;
  clan: any = {};
  isVisibleRankingClan: any;
  hasChangePicture: boolean = false;

  constructor(public navCtrl: NavController, 
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public api: Api,
    public params: NavParams,
    public storage: Storage,
    public actionSheetCtrl: ActionSheetController, 
    public camera: Camera,
    public clanProvider: Clan,
    public userProvider: Users,
    public rankProvider: Rank,
    public translate: Translate) {
      if(params.get('clan')){
        this.clan = clanProvider.getCurrentClan();
      }else{
        this.clan = {};
        clanProvider.resetClan();
      }
  }

  ionViewWillLeave() {
  }


  setDefaultClan(flag){
  }

  close() {
  }

  editClan(){
    this.clanProvider.setDescription(this.clan.description);
    this.clanProvider.setName(this.clan.name);
    let data: any;
    data = {};
    data.clan = this.clanProvider.getCurrentClan();
    data.userId = this.userProvider.getId();

    if(this.hasChangePicture){
      data.image = this.clanProvider.getImage();
    }
    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: this.translate.getTranslate('BUTTON.LOADING_EDIT_CLAN'),
      dismissOnPageChange: true
    });
    loading.present();
    this.api.post('clan/update', data)
    .subscribe(
      (result) => {
        let body: any;
        body = JSON.parse(result.text());
        if(body.error){
          loading.dismiss();
          this.doAlert(body.message.text);
        }else{
          this.clanProvider.setCurrentClan(body);
          this.viewCtrl.dismiss();
        }
      },
      (err) => {
        this.doAlert(err.message);
      },
      () => {
      }
      );
  }

  createClan(){
    let data: any;
    data = {};
    data = this.clan;
    data.userId = this.userProvider.getId();

    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: this.translate.getTranslate('BUTTON.LOADING_CREATE_CLAN'),
      dismissOnPageChange: true
    });

    loading.present();
    this.api.post('clan/create', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());
        if(body.error){
          loading.dismiss();
          this.doAlert(body.message.text);
        }else{
          this.clanProvider.setCurrentClan(body);
          this.doAlert(this.translate.getTranslate('PAGE.CLAN.CREATE_CLAN_MESSAGE'), 'Bravo');
          this.rankProvider.loadAllRanks();
          this.viewCtrl.dismiss();
        }
      },
      (err) => {
        loading.dismiss();
        this.doAlert(err.message);
      },
      () => {
      }
      );
  }

  clanDelete(){
    let data: any;
    data = {};
    data.userId = this.userProvider.getId();
    data.clanId = this.clanProvider.getId();
    let alert = this.alertCtrl.create({
      title: this.translate.getTranslate('PAGE.CLAN.DELETE_CLAN'),
      message: this.translate.getTranslate('PAGE.CLAN.DELETE_CLAN_ALERT'),
      buttons: [
      {
        text: this.translate.getTranslate('BUTTON.CANCEL'),
        role: 'cancel',
        handler: data => {
        }
      },
      {
        text: this.translate.getTranslate('BUTTON.DELETE'),
        handler: () => {
          this.api.post('clan/delete', data)
          .subscribe(
            (data) => {
              let body: any;
              body = JSON.parse(data.text());
              if(!body.error){
                // Si ce clan était le classement par défaut par défaut, on remet Monde comme classement par défaut
                if(this.clanProvider.getId() == this.rankProvider.getId()){
                  this.storage.set(appSettings.STORAGE_ATTRIBUTES.DEFAULT_RANKING, 1);
                  // Chargement de la place de l'utilisateur dans son profil
                  this.rankProvider.loadRank(this.userProvider.getUser(), 1);
                }

                this.navCtrl.setRoot(ProfilePage);
              }
            },
            (err) => {
            },
            () => {
            }
            );
        }
      }
      ]
    });
    alert.present();
  }

  openPhotoPicker() {
    let actionSheet = this.actionSheetCtrl.create({
      title: this.translate.getTranslate('PLUGIN.CAMERA.PICTURE_CLAN'),
      buttons: [
      {
        text: this.translate.getTranslate('PLUGIN.CAMERA.CHOOSE_PICTURE'),
        handler: () => {
          const options: CameraOptions = {
            sourceType: 0,
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            correctOrientation: true,
            targetWidth: 500,
            targetHeight: 500
          }
          this.camera.getPicture(options).then((imageData) => {
            let base64Image = 'data:image/jpeg;base64,' + imageData;
            //this.profileImages[index].image = base64Image;
            this.clanProvider.setImage(base64Image);
            this.clan = this.clanProvider.getCurrentClan();
            this.hasChangePicture = true;
          }, (err) => {
            // Handle error
          });
        }
      },
      {
        text: this.translate.getTranslate('PLUGIN.CAMERA.TAKE_PICTURE'),
        handler: () => {
          const options: CameraOptions = {
            sourceType: 1,
            quality: 100,
            destinationType: this.camera.DestinationType.DATA_URL,
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            correctOrientation: true,
            targetWidth: 500,
            targetHeight: 500
          }
          this.camera.getPicture(options).then((imageData) => {
            // imageData is either a base64 encoded string or a file URI
            // If it's base64 (DATA_URL):
            let base64Image = 'data:image/jpeg;base64,' + imageData;
            //this.profileImages[index].image = base64Image;
            this.clanProvider.setImage(base64Image);
            this.clan = this.clanProvider.getCurrentClan();
            this.hasChangePicture = true;
            // Test insert BACK

          }, (err) => {
            // Handle error
          });
        }
      },
      {
        text: this.translate.getTranslate('BUTTON.CANCEL'),
        role: 'cancel',
        handler: () => {
        }
      }
      ]
    });

    actionSheet.present();
  }

  doAlert(message, title='Aïe!') {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: message,
      buttons: ['Ok']
    });

    alert.present();
  }

}


