import { Injectable } from '@angular/core';
import {
	App, ModalController
} from 'ionic-angular';
import { appSettings } from '../../app/app-settings';
import { Api } from '../api/api';
import { Users } from '../users/users';
import { Storage } from "@ionic/storage";
import { PlayPage, LogPage, ProfilePage, TutorielProfileStep1Page, TutorielProfileStep2Page, 
	TutorielProfileStep3Page, TutorielProfileStep4Page, TutorielProfileStep5Page,
	TutorielProfileStep6Page, TutorielProfileStep7Page, TutorielProfileStep8Page,
	TutorielProfileStep9Page
} from "../../pages/pages";
import {PoperProfilePage} from "../../pages/poperprofile/poperprofile";

@Injectable()
export class Tutoriel {
	active: boolean = false;

	currentStep: number = 0;

	step1: any;
	step2: any;
	step3: any;
	step4: any;
	step5: any;
	step6: any;
	step7: any;
	step8: any;
	step9: any;

	constructor(
		public api: Api,
		public storage: Storage,
		public modalCtrl: ModalController,
		private app: App,
		public userProvider: Users) {
		this.step1 = this.modalCtrl.create(TutorielProfileStep1Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step2 = this.modalCtrl.create(TutorielProfileStep2Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step3 = this.modalCtrl.create(TutorielProfileStep3Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step4 = this.modalCtrl.create(TutorielProfileStep4Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step5 = this.modalCtrl.create(TutorielProfileStep5Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step6 = this.modalCtrl.create(TutorielProfileStep6Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step7 = this.modalCtrl.create(TutorielProfileStep7Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step8 = this.modalCtrl.create(TutorielProfileStep8Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
		this.step9 = this.modalCtrl.create(TutorielProfileStep9Page, {}, {showBackdrop: true, enableBackdropDismiss: true, cssClass: 'modal-tutoriel' });
	}

	isTutoriel(){
		return this.active;
	}

	setTutoriel(){
		this.active = true;
	}

	quitTutoriel(){
		this.active = false;
	}

	getCurrentStep(){
		return this.currentStep;
	}

	nextStep(options){
		if(options.step !== undefined){
			this.currentStep = options.step;
		}else{
			this.currentStep++;

		}
		console.log('step : '+this.currentStep);
		switch(this.currentStep){
			case 1:{
				this.step1.present();
				break;
			}
			case 2:{
				this.step1.dismiss();
				this.step2.present();
				break;
			}
			case 3:{
				this.step2.dismiss();
				this.changePage(PlayPage);
				this.step3.present();
				let pop: any;
				pop = {};
				this.api.post('forcepop/' + this.userProvider.getId(), pop)
				.subscribe(
					(pop) => {
					},
					(err) => {
						console.log(err);
						//this.navCtrl.setRoot(this.navCtrl.getActive().component);
					},
					() => {
						//this.goToHome();
					}
					);
				break;
			}
            case 4:{
                this.step3.dismiss();
                this.changePage(LogPage);
                this.step5.present();
                break;
            }
            case 5:{
                this.step5.dismiss();
                this.changePage(ProfilePage);
                this.step8.present();
                break;
            }
            case 6:{
                this.step9.present();
                break;
            }
            default:{
                break;
            }
		}
	}

	changePage(page){
		this.app.getActiveNavs()[0].push(page, {}, {
			direction: 'forward'
		});
	}

	passTuto(){
		this.storage.remove(appSettings.STORAGE_ATTRIBUTES.TUTORIEL);
	}
}



